/** @fileoverview The stuff related to the login and logout procedure
 *  @externs
 *  @module
 */
import {ContractInstance} from './local_storage.js';
import Utils from './utils.js';

export default {
  /** The factory for a Function object
   * @func Init
   * @param {Array<Element>} components Components, whose has_logined property should be managed
   * @param {Element} super_component The super component
   * @return {Function} The function, which should be bound to a login-logout event
   */
  Init(components, super_component) {
    return () => {
      // Log out:
      if (!super_component.has_logined) {
        Moralis.LiveQuery.close(); // Close a WebSocket for all subscriptions
        super_component.current_user = null;
        super_component.current_address = null;
        super_component.web3_wrappers = {};
        super_component.contract_instance = null;
        components.map((component)=>{
          component.has_logined = false;
        });
        Moralis.User.logOut().then(()=>{
          console.log("Logged out");
        });
        return;
      }
      // Log In:
      Moralis.Web3.authenticate().then((current_user) => {
        console.log("The current user: ", current_user);
        console.log("The current user password: ", current_user.password);
        console.log("The current user's EthAddress: ", current_user.get("ethAddress"));
        super_component.current_user = current_user;
        super_component.current_address = current_user.get("ethAddress"); // Utils.get_current_address().then();
        Utils.init_web3_wrappers(super_component.web3_wrappers, super_component.current_address);
        super_component.contract_instance = new ContractInstance();
        /// Fetch the smart contract instance
        fetch('https://webatix.github.io/test/smart-contract/deployed-instance.json')
          .then((response)=>{
            if (!response.ok) return;
            response.json().then((fetched_deployed_contract_instance_parsed_object)=>{
              super_component.contract_instance.value = fetched_deployed_contract_instance_parsed_object;
              console.log("The fetched deployed contract instance == ", super_component.contract_instance.value);
            });
          });
        /// Send to all components, that they are logined
        components.map((component)=>{
          component.has_logined = true;
        });
        Moralis.Cloud.run(
          'watchEthAddress',
          {"address": current_user.get("ethAddress")
        }).then((results) => {
          console.log("The watchEthAddress results: ", results);
          getUserTransactions(current_user);
        }).catch(() => {
        }); // OnWatchAddress
      }); // OnAuth
    }; // A lambda object is returned
  } // Init
}
