import {LitElement, html, css, render as LitRender} from 'lit';
import {customElement, property, query} from 'lit/decorators.js';
import '@kor-ui/kor';
import AppTemplates from '../templates/app.js';
import { Profile } from '../moralis_objects.js';
import Utils from '../utils.js';
import { sharedStyles } from '../shared_styles.js';

/** @fileoverview The component for a schedule managing
 * Generates the ${name}-changed event when a property ${name} was changed
 * @externs
 * @module
 * @prop {boolean} visible Whether this component is visible
 * @prop {boolean} has_logined Whether a user has logined
 * @prop {Object} current_user The current user value
 * @prop {Element} schedule_tools Tools container
 * @prop {Element} schedule_details Schedule table container
 * @prop {Element} add_schedule_form The "Add a payment" form container
 * @prop {Element} notification_container This component notification container
 * @prop {Element} transaction_content The dynamically generated transaction content container
 * @prop {Element} schedule_content The dynamically generated schedule content container
 * @prop {BookedAddresses} booked_addresses The addresses already used to make transactions as an instance of the BookedAddresses class backed by the Local Storage
 * @prop {Web3Wrappers} web3_wrappers The object to represent different web3 wrappers
 * @prop {ScheduleTable} schedule_table The object, which stores the table of schedule items as an instance of the ScheduleTable class backed by the Local Storage
 * @private @prop {TransactionData} transaction_data_ The transaction's details temporary object
 * @private @prop {ScheduleData} schedule_data_ The schedule's details temporary object
 * @see module:../type_def.h
 */
@customElement('dapp-summary')
export default class dappSummary extends LitElement {
  @property({type: Boolean, reflect: true}) visible = true;
  @property({type: Boolean, reflect: true}) has_logined = false;
  @property({type: Object, reflect: true}) current_user = null;
  @query("#summaryTools", false) summary_tools;
  @query("#summaryContent", false) summary_content;
  @query("input", true) file_input;
  @query("kor-avatar", true) avatar;
  /// Don't reflect these properties to HTML attributes (avoid a big data's duplication),
  /// don't update this component on these properties changes
  @property({type: Object, reflect: false, noAccessor: true}) notification_container = null;
  /// Private
  @property({type: Object, reflect: false, noAccessor: true, state: true}) profile_ = new Profile();
  @property({type: Array, reflect: false, noAccessor: true, state: true}) errors_ = [];
  @property({type: Array, reflect: false, noAccessor: true, state: true}) successes_ = [];

  static styles =
    [sharedStyles,
     css`
       input[type="file"] {
         display: block;
         visibility: hidden;
         width: 0;
         height: 0;
       }`
    ];

  render() {
    return html`
      <div id="summaryTools" grid-rows="1" style="display: block">
        <kor-icon class="heading_icon" icon="account_balance" color="var(--text-1)" size="m"></kor-icon>
        <kor-card class="heading_tools" flat>
          <kor-avatar label="User" info="User account"
            @click="${(e)=>{
              this.file_input.click();
            }}"
          ></kor-avatar>
          <input type="file" @change="${this.handleAvatarUploading}">
        </kor-card>
      </div>
      <div id="summaryContent" grid-rows="9" style="display: block"></div>`;
  }

  constructor() {
    super();
  }

  connectedCallback() {
    super.connectedCallback();
    if (!this.style) this.style="display: block;";
    Utils.setup_component(this);
  }

  loadProfile2Moralis() {
    if (!this.has_logined || !this.current_user) return;
    /** @const {Object} */
    const relation = this.current_user.relation("profile");
    relation.query().find().then((profiles_list)=>{
      profiles_list.forEach((profile, index, profiles)=>{
        // Take the last profile and delete others
        this.profile_ = profile;
        if (index) {
          profiles[index - 1].destroy();
        }
      });
      this.avatar.image = this.profile_.avatar_image;
    });
  }

  saveProfile2Moralis() {
    if (!this.has_logined || !this.current_user) {
      this.errors_ = this.errors_.concat("To save your profile you must be logined");
      Utils.error_notifications(this.errors_, this.notification_container);
      return;
    }
    this.profile_.avatar_image = this.avatar.image;
    this.profile_.save().then((profile)=>{
      /** @const {Object} */
      const relation = this.current_user.relation("profile");
      relation.add(this.profile_);
      this.current_user.save();
      this.successes_ = this.successes_.concat("The profile was successfully saved");
      Utils.success_notifications(this.successes_, this.notification_container);
    }, (error)=>{
      this.errors_ = this.errors_.concat("Unable to save the profile");
      Utils.error_notifications(this.errors_, this.notification_container);
    });
  }

  handleAvatarUploading() {
    /** @const {Object} */
    const file_reader  = new FileReader();
    file_reader.onloadend = () => {
      if (!file_reader.result) {
        this.errors_ = this.errors_.concat("A file reading error");
        Utils.error_notifications(this.errors_, this.notification_container);
        return;
      }
      this.avatar.image = file_reader.result;
      this.saveProfile2Moralis();
    }; // file_reader.onloadend
    /** @const {?Object} */
    const image_file = this.file_input.files[0];
    if (!image_file) {
      this.errors_ = this.errors_.concat("Please specify a file with an image for the avatar");
      Utils.error_notifications(this.errors_, this.notification_container);
      return;
    }
    file_reader.readAsDataURL(image_file);
  }

  attributeChangedCallback(name, oldval, newval) {
    super.attributeChangedCallback(name, oldval, newval);
    if (name == 'has_logined') {
      if (this.has_logined == true) {
        Utils.login_component(this);
        LitRender(
          AppTemplates.object_entries_two_columns(Object.entries(this.current_user)),
          this.summary_content);
        this.loadProfile2Moralis();
      } else {
        Utils.logout_component(this);
        LitRender(html``, this.summary_content);
        this.avatar.image = null;
      }
    }
    if (name == 'visible' && this.summary_tools && this.summary_content) {
      if (this.visible == true) {
        this.style.display = "block";
        if (this.has_logined == true) {
          this.summary_tools.style.display = "block";
          this.summary_content.style.display = "block";
        } else {
          this.summary_tools.style.display = "block";
          this.summary_content.style.display = "none";
        }
      } else {
        this.summary_tools.style.display = "none";
        this.summary_content.style.display = "none";
        this.style.display = "none";
      }
    }
    this.dispatchEvent(new Event(`${name}-changed`));
  }
}
