import {LitElement, html, css} from 'lit';
import {customElement, property, query} from 'lit/decorators.js';
import '@kor-ui/kor';
import Utils from '../utils.js';

/** @fileoverview The component for a smart contract method createSubscriptionProduct() invoking
 * Generates the subscription-product-created event on a success
 * Generates the ${name}-changed event when a property ${name} was changed
 * @externs
 * @module
 * @prop {boolean} has_logined Whether a user has logined
 * @prop {string} theme The "light" and "dark" enumeration
 * @prop {string} current_address The address, from which the contract's method will be invoked
 * @prop {number} productID The product ID value after the product is successfully created
 * @prop {Element} open_button The button which opens a user form
 * @prop {Element} user_form The form to fill for the method invoking
 * @prop {Element} notification_container The container where to display notifications
 * @prop {Object} contract_instance The contract instance to invoke
 * @private @prop {number} amount_gwei_
 * @private @prop {number} billing_interval_secs_
 * @private @prop {number} gas_price_gwei_
 * @private @prop {Array<(string|Object)>} errors_
 * @private @prop {Array<(string|Object)>} successes_
 */
@customElement('dapp-create-subscription-product')
export default class dappCreateSubscriptionProduct extends LitElement {
  @property({type: Boolean, reflect: true}) has_logined = false;
  @property({type: String, reflect: true}) theme = "light";
  @property({type: String, reflect: true}) current_address = null;
  @property({type: Number, reflect: true}) productID = null;
  @query("kor-icon", true) open_button;
  @query("kor-modal", false) user_form;
  /// Don't reflect these properties to HTML attributes (avoid a big data's duplication),
  /// don't update this component on these properties changes
  @property({type: Object, reflect: false, noAccessor: true}) notification_container = null;
  @property({type: Object, reflect: false, noAccessor: true}) contract_instance = null;
  /// Private
  @property({type: Object, reflect: false, noAccessor: true, state: true}) amount_gwei_ = 1;
  @property({type: Object, reflect: false, noAccessor: true, state: true}) billing_interval_secs_ = 1;
  @property({type: Number, reflect: false, noAccessor: true, state: true}) gas_price_gwei_ = 1;
  @property({type: Array, reflect: false, noAccessor: true, state: true}) errors_ = [];
  @property({type: Array, reflect: false, noAccessor: true, state: true}) successes_ = [];
  ///

  render() {
    return html`
      <kor-icon icon="touch_app" button style="display: none"
        @click="${(e)=>{
          this.user_form.setAttribute("visible", "true");
        }}"
      ></kor-icon>
      <kor-modal icon="touch_app" label="Add a new product" theme="${this.theme}">
        <kor-card flat>
          <kor-grid columns="1" fit-content>
            <kor-card grid-cols="1" flat>
              <kor-input label="Amount in GWEI" type="number" min=1 value="${this.amount_gwei_}"
                @change="${(e)=>{
                  this.amount_gwei_ = parseInt(e.target.value);
                }}"
              ></kor-input>
              <kor-input label="Billing interval in seconds" type="number" min=1 value="${this.billing_interval_secs_}"
                @change="${(e)=>{
                  this.billing_interval_secs_ = parseInt(e.target.value);
                }}"
              ></kor-input>
              <kor-input label="Gas price in GWEI" type="number" min=1 value="${this.gas_price_gwei_}"
                @change="${(e)=>{
                  this.gas_price_gwei_ = parseInt(e.target.value);
                }}"
              ></kor-input>
            </kor-card>
          </kor-grid>
        </kor-card>
        <kor-button label="Create" color="Primary" slot="footer"
          @click="${(e)=>{
            if (!this.contract_instance) {
              this.errors_ = this.errors_.concat("Please set a contract instance into this component");
              Utils.error_notifications(this.errors_, this.notification_container);
              return;
            }
            /** @const {Object} */
            const transaction_object =
              this.contract_instance.methods.createSubscriptionProduct(
                this.amount_gwei_ * Math.pow(10,9), // GWEI -> WEI
                this.billing_interval_secs_
              );
            console.log("The transaction_object == ", transaction_object);
            Utils.send_transaction(
              transaction_object, // A transaction object to pass as an argument
              this.current_address, this.gas_price_gwei_, 0 /* value_gwei */,
              this.errors_, this.successes_, this.notification_container
            ).then((receipt)=>{
              this.successes_ = this.successes_.concat(JSON.stringify(receipt));
              Utils.success_notifications(this.successes_, this.notification_container);
              console.log(receipt);
              this.productID = receipt.events.subscriptionProductCreated.returnValues.productID;
              this.dispatchEvent(new Event('subscription-product-created',
                {bubbles: true, composed: true}));
            });
            this.user_form.removeAttribute("visible");
          }}"
        ></kor-button>
      </kor-modal>`;
  }

  constructor() {
    super();
  }

  connectedCallback() {
    super.connectedCallback()
    Utils.setup_component(this);
  }

  attributeChangedCallback(name, oldval, newval) {
    super.attributeChangedCallback(name, oldval, newval);
    if (name == 'has_logined') {
      if (this.has_logined == true) {
        this.errors_ = [];
        this.successes_ = [];
        Utils.login_component(this);
        this.open_button.style.display = "block";
      } else {
        this.open_button.style.display = "none";
        Utils.logout_component(this);
      }
    }
    if (name == 'theme' && this.user_form) {
      this.user_form.theme = this.theme;
    }
    this.dispatchEvent(new Event(`${name}-changed`));
  }
}
