import {LitElement, html} from 'lit';
import {customElement, property, query} from 'lit/decorators.js';
import '@kor-ui/kor';

/** @fileoverview The component for DApp's menu implementation
 * Generates the ${name}-changed event when a property ${name} was changed
 * @externs
 * @module
 * @prop {boolean} has_logined Whether a user has logined
 * @prop {Element} app_menu_tooltip
 * @prop {Element} login_menu_item
 */
@customElement('dapp-menu')
export default class dappSwitchChain extends LitElement {
  @property({type: Boolean, reflect: true}) visible = true;
  @property({type: Boolean, reflect: true}) has_logined = false;
  @query("#invokeButton", true) invoke_button;
  @query("#appMenuTooltip", true) app_menu_tooltip;
  @query("#appMenu", true) app_menu;
  @query("#loginMenuItem", true) login_menu_item;

  render() {
    return html`
      <kor-icon id="invokeButton" slot="functions" icon="menu" button
        @click="${(e)=>{
          this.app_menu_tooltip.setAttribute("visible", "false");
        }}"
      ></kor-icon>
      <kor-tooltip id="appMenuTooltip" position="left">DApp menu</kor-tooltip>
      <kor-popover id="appMenu" position="left" label="DApp menu" icon="menu">
        <kor-menu-item id="loginMenuItem" label="Login using Metamask" icon="person" toggle="false"
          @click="${(e)=>{
            this.has_logined ^= true; // Toggle the property value with XOR operation
            this.app_menu.visible = false;
          }}"
        ></kor-menu-item>
        <kor-menu-item label="Preferences" icon="work" toggle="false"
          @click="${(e)=>{
            this.app_menu.visible = false;
          }}"
        ></kor-menu-item>
      </kor-popover>`;
  }

  constructor() {
    super();
  }

  connectedCallback() {
    super.connectedCallback();
    /** @const {NodeList} */
    const undefinedElements =
      this.shadowRoot.querySelectorAll(':not(:defined)');
    /** @const {Array<Promise>} */
    const promises = [...undefinedElements].map(element => {
      return customElements.whenDefined(element.localName);
    });
    /// Invoke the code below after this.shadowRoot is rendered
    Promise.all(promises).then(() => {
      this.app_menu_tooltip.target = this.invoke_button;
      this.app_menu.target = this.invoke_button;
    });
  }

  attributeChangedCallback(name, oldval, newval) {
    super.attributeChangedCallback(name, oldval, newval);
    if (name == 'has_logined') {
      if (this.has_logined) {
        this.login_menu_item.label = "Logout";
      } else {
        this.login_menu_item.label = "Login using Metamask";
      }
    }
    this.dispatchEvent(new Event(`${name}-changed`));
  }
}

