/** @fileoverview Dynamic GUI definition for subscriptions
 *  @externs
 *  @module
 *  @see module:../type_def.h
 */
import '@kor-ui/kor';
import '@kor-ui/kor/kor-styles.css';
import {html} from 'lit';
import '../components/unsubscribe.js';

export default {
  /** Generates a subscriptions view
   *
   * @callback subscription_details_callback
   * @param {Array<string>} subscription
   *
   * @callback subscriptions_refresh_callback
   *
   * @func subscriptions
   * @param {Array<Array<string>>} subscriptions_array
   * @param {subscription_details_callback} subscription_details
   * @param {subscriptions_refresh_callback} subscriptions_refresh
   * @return {Object}
   */
  subscriptions(subscriptions_array, subscription_details, subscriptions_refresh) {
    return html`
      <kor-grid columns="1">
        <kor-card grid-cols="1" flat>
          <kor-text size="header-1">Current subscriptions</kor-text>
          <kor-table columns="4fr 3fr 3fr 2fr 2fr 1fr">
            <kor-table-row slot="header">
              <kor-table-cell head>Vendor address</kor-table-cell>
              <kor-table-cell head>Amount</kor-table-cell>
              <kor-table-cell head>Billing Interval in secs</kor-table-cell>
              <kor-table-cell head>Is Active</kor-table-cell>
              <kor-table-cell head>ID</kor-table-cell>
              <kor-table-cell head></kor-table-cell>
            </kor-table-row>
            ${subscriptions_array.map(
              (subscription) => html`
                <kor-table-row
                  @click="${(e)=>{
                    if (e.composedPath()[2].tagName != "DAPP-UNSUBSCRIBE")
                      subscription_details(subscription);
                  }}"
                >
                  <kor-table-cell>${subscription[4]}</kor-table-cell>
                  <kor-table-cell>${subscription[6]}</kor-table-cell>
                  <kor-table-cell>${subscription[7]}</kor-table-cell>
                  <kor-table-cell>${subscription[1]}</kor-table-cell>
                  <kor-table-cell>${subscription[0]}</kor-table-cell>
                  <kor-table-cell>
                    <dapp-unsubscribe subscription_id="${subscription[0]}"
                      @unsubscribed="${(e)=>{
                        subscriptions_refresh();
                      }}"
                    ></dapp-unsubscribe>
                  </kor-table-cell>
                </kor-table-row>`
            )}
          </kor-table>
        </kor-card>
      </kor-grid>`;
  }
}
