import {LitElement, html, css, render as LitRender} from 'lit';
import {customElement, property, query} from 'lit/decorators.js';
import '@kor-ui/kor';
import AppTemplates from '../templates/app.js';
import TransactionsTemplates from '../templates/transactions.js';
import Utils from '../utils.js';
import { sharedStyles } from '../shared_styles.js';

/** @fileoverview The component for inspecting transactions
 * Generates the ${name}-changed event when a property ${name} was changed
 * @externs
 * @module
 * @prop {boolean} visible Whether this component is visible
 * @prop {boolean} has_logined Whether a user has logined
 * @prop {string} theme The "light" and "dark" enumeration
 * @prop {string} current_address The address, from which the transaction will be inspected
 * @prop {Element} transactions_tools Tools container
 * @prop {Element} transactions_content Transactions tables container
 * @prop {Element} transaction_details_modal The modal window to show details of a particular transaction
 * @prop {Element} transaction_details_content The container with details of a particular transaction in the modal window
 * @see module:../type_def.h
 */
@customElement('dapp-transactions')
export default class dappTransactions extends LitElement {
  @property({type: Boolean, reflect: true}) visible = false;
  @property({type: Boolean, reflect: true}) has_logined = false;
  @property({type: String, reflect: true}) theme = "light";
  @property({type: String, reflect: true}) current_address = null;
  @query("#transactionsTools", false) transactions_tools;
  @query("#transactionsContent", false) transactions_content;
  @query("#transactionDetailsModal", false) transaction_details_modal;
  @query("#transactionDetailsContent", true) transaction_details_content;

  static get styles() {
    return [sharedStyles,
            css``];
  }

  render() {
    return html`
      <div id="transactionsTools" grid-rows="1" style="display: none">
        <kor-icon class="heading_icon" icon="account_balance_wallet" color="var(--text-1)" size="m"></kor-icon>
        <kor-card class="heading_tools" flat>
          <kor-button label="Set up a display filter" color="Primary"></kor-button>
        </kor-card>
      </div>
      <div id="transactionsContent" grid-rows="9" style="display: none"></div>
      <kor-modal id="transactionDetailsModal" icon="list" label="The transaction's details" theme="light">
        <kor-card id="transactionDetailsContent" flat></kor-card>
      </kor-modal>`;
  }

  /** Renders the
   * @func transaction_details details of a particular transaction in the modal window
   * @param {Object} transaction */
  transaction_details(transaction) {
    LitRender(
      AppTemplates.object_entries_two_columns(Object.entries(transaction.attributes)),
      this.transaction_details_content);
    this.transaction_details_modal.setAttribute("visible", "true");
  }

  /** @func getUserTransactions - async */
  async getUserTransactions() {
    /** Create the query for historical incoming transactions
     * @const {Object} */
    const query_in = new Moralis.Query("EthTransactions");
    query_in.equalTo("to_address", this.current_address);
    /** Create the query for historical out coming transactions
     * @const {Object} */
    const query_out = new Moralis.Query("EthTransactions");
    query_out.equalTo("from_address", this.current_address);
    /** Find incoming transactions
     * @type {Object}
     */
    let in_transactions = await query_in.find();
    console.log("Incoming transactions: ", in_transactions);
    /** Find out coming transactions
     * @type {Object}
     */
    let out_transactions = await query_out.find();
    console.log("Out coming transactions: ", out_transactions);
    // Render the transactions data into the transactions page
    LitRender(
      TransactionsTemplates.transactions(
        in_transactions, out_transactions, (transaction)=>{this.transaction_details(transaction)}),
      this.transactions_content);
    /** Subscribe for the real-time updates of incoming transactions
     * @const {Object}
     */
    const in_subscription = await query_in.subscribe();
    /** Subscribe for the real-time updates of out coming transactions
     * @const {Object}
     */
    const out_subscription = await query_out.subscribe();
    in_subscription.on("create", (new_transaction) => {
      console.log("A new incoming transaction: ", new_transaction);
      in_transactions = in_transactions.concat(new_transaction);
      // Render the transactions data into the transactions page
      LitRender(
        TransactionsTemplates.transactions(
          in_transactions, out_transactions, (transaction)=>{this.transaction_details(transaction)}),
        this.transactions_content);
    }); // OnInSubscription
    out_subscription.on("create", (new_transaction) => {
      console.log("A new out coming transaction: ", new_transaction);
      out_transactions = out_transactions.concat(new_transaction);
      // Render the transactions data into the transactions page
      LitRender(
        TransactionsTemplates.transactions(
          in_transactions, out_transactions, (transaction)=>{this.transaction_details(transaction)}),
        this.transactions_content);
    });
  }

  constructor() {
    super();
  }

  connectedCallback() {
    super.connectedCallback();
    if (!this.style) this.style="display: none;";
    Utils.setup_component(this);
  }

  attributeChangedCallback(name, oldval, newval) {
    super.attributeChangedCallback(name, oldval, newval);
    if (name == 'has_logined') {
      if (this.has_logined == true) {
        Utils.login_component(this);
        this.getUserTransactions();
      } else {
        Utils.logout_component(this);
        LitRender(html``, this.transactions_content);
      }
    }
    if (name == 'visible') {
      this.style.display = "block";
      if (this.visible == true) {
        if (this.has_logined == true) {
          this.transactions_tools.style.display = "block";
          this.transactions_content.style.display = "block";
        } else {
          this.transactions_tools.style.display = "block";
          this.transactions_content.style.display = "none";
        }
      } else {
        this.transactions_tools.style.display = "none";
        this.transactions_content.style.display = "none";
        this.style.display = "none";
      }
    }
    if (name == 'theme' && this.transaction_details_modal) {
      this.transaction_details_modal.theme = this.theme;
    }
    this.dispatchEvent(new Event(`${name}-changed`));
  }
}
