export class Profile extends Moralis.Object {
  constructor() {
    // Pass the ClassName to the Moralis.Object constructor
    super('Profile');
    this.set("avatar_image_", null);
  }

  get avatar_image() {
    return this.get("avatar_image_");
  }
  /** @param {Object} avatar_image DataUrl of the avatar image */
  set avatar_image(avatar_image) {
    this.set("avatar_image_", avatar_image);
  }
}

// Register the subclasses above to use it from queries
try {
  Moralis.Object.registerSubclass('Profile', Profile);
} catch {
  // The subclasses above have been already registered
}
