/** @fileoverview Dynamic GUI definition for schedule
 *  @externs
 *  @module
 *  @see module:../type_def.h
 *  @see module:../local_storage
 */
import '@kor-ui/kor';
import '@kor-ui/kor/kor-styles.css';
import {html} from 'lit';
import Utils from '../utils.js';

export default {
  /** Generates a schedule content into the add schedule modal window
   *  with a binding to the schedule_data object
   * @param {ScheduleData} schedule_data
   * @return {Object}
   */
  add_schedule(schedule_data) {
    return html`
      <kor-grid columns="4">
        <kor-text grid-cols="1" size="body-1">Start since:</kor-text>
        <input grid-cols="2" type="date"
          @change="${(e)=>{
            schedule_data.start_date = e.target.value;
          }}"
        >
        <kor-text grid-cols="4" size="body-1">at 12:00 PM</kor-text>
        <kor-slider grid-cols="4" value="720" min="0" max="1440" step="1"
          @value-changed="${(e)=>{
            schedule_data.start_time = Number.parseInt(e.target.value);
            /** @const {Clock} */
            const clock =
              Utils.start_time_to_clock(schedule_data.start_time)
            /** @const {Element} */
            const label_element =
              e.target.parentElement.querySelectorAll('kor-text')[1];
            label_element.innerHTML =
              "at " + clock.hour.toFixed(0).padStart(2, '0') + ":" + clock.minute.toFixed(0).padStart(2, '0') + " " + clock.am_pm;
          }}"
        >
        </kor-slider>
        <kor-toggle grid-cols="4" label="Should be repeated"
          @active-changed="${(e)=>{
            /** @const {Element} */
            const interval_card =
              e.target.parentElement.querySelector('kor-card');
            /** @const {Element} */
            const interval_element =
              interval_card.querySelector('kor-slider');
            if (e.target.active) {
              interval_card.style.display = "flex";
              schedule_data.interval_days =
                interval_element.value;
            } else {
              interval_card.style.display = "none";
              schedule_data.interval_days = 0;
            }
          }}"
        >
        </kor-toggle>
        <kor-card grid-cols="4" style="display: none" flat>
          <kor-grid columns="1">
            <kor-text grid-cols="1" size="body-1">Repeat each 30 days</kor-text>
            <kor-slider grid-cols="1" value="30" min="0" max="366" step="1"
              @value-changed="${(e)=>{
                schedule_data.interval_days = Number.parseInt(e.target.value);
                /** @const {Element} */
                const label_element =
                  e.target.parentElement.querySelector('kor-text');
                label_element.innerHTML =
                  "Repeat each " + schedule_data.interval_days.toFixed(0) + " days";
              }}"
            >
            </kor-slider>
          </kor-grid>
        </kor-card>
      </kor-grid>`;
  },
  /** Generates a schedule table with a binding to the schedule_table array
   * @param {ScheduleTable} schedule_table The instance of the ScheduleTable class backed by the Local Storage
   * @param {Function} refresh_schedule The callback to refresh the schedule view
   * @return {Object}
   */
  schedule_table(schedule_table, refresh_schedule) {
    return html`
      <kor-card flat>
        <kor-text size="header-1">Schedule table</kor-text>
        <kor-table columns="repeat(5, 1fr) auto">
          <kor-table-row slot="header">
            <kor-table-cell head>To address</kor-table-cell>
            <kor-table-cell head>Amount in GWEI</kor-table-cell>
            <kor-table-cell head sortable sorted sort-direction="desc">Start date</kor-table-cell>
            <kor-table-cell head>Start time</kor-table-cell>
            <kor-table-cell head>Days interval</kor-table-cell>
            <kor-table-cell head></kor-table-cell>
          </kor-table-row>
          ${schedule_table.value.map(
            (schedule_item) => html`
              <kor-table-row>
                <kor-table-cell>${schedule_item.transaction_data.destination_address}</kor-table-cell>
                <kor-table-cell>${schedule_item.transaction_data.amount_gwei}</kor-table-cell>
                <kor-table-cell>${schedule_item.schedule_data.start_date}</kor-table-cell>
                <kor-table-cell>${
                  (()=>{
                    /** @const {Clock} */
                    const clock = Utils.start_time_to_clock(schedule_item.schedule_data.start_time);
                    return clock.hour.toFixed(0).padStart(2, '0') + ":" + clock.minute.toFixed(0).padStart(2, '0') + " " + clock.am_pm;
                  })()
                }</kor-table-cell>
                <kor-table-cell>${schedule_item.schedule_data.interval_days}</kor-table-cell>
                <kor-table-cell>
                  <kor-icon icon="delete" button
                    @click="${(e)=>{
                      schedule_table.remove(schedule_item);
                      setTimeout(refresh_schedule, 500);
                    }}"
                  ></kor-icon>
                </kor-table-cell>
              </kor-table-row>`
          )}
        </kor-table>
      </kor-card>`;
  }
}
