/** @fileoverview Storages classes backed by the Local Storage
 *  @externs
 *  @module
 *  @see module:type_def.h
 */

/** The base class for all other storages classes,
 *  which use the Local Storage to store their value properity */
class LocalStorageBacked {
  constructor() {
    /** @type {?(Object|Array)} */
    this.cached_value_ =
      JSON.parse(window.localStorage.getItem(this.storage_name));
  }

  /** The storage_name getter
   * @return {string}
   */
  get storage_name() {
    return "LocalStorageBacked";
  }

  /** Syncs the cached value with the storage and returns the synced value
   * @return {?(Object|Array)} */
  get storage_value() {
    /** @type {?(Object|Array)} */
    this.cached_value_ =
      JSON.parse(window.localStorage.getItem(this.storage_name));
    return this.cached_value_;
  }
}

/** The std::set of unique booked addresses,
 *  each address is stored as an Object key in order to remove duplicates,
 * @extends LocalStorageBacked
 */
export class BookedAddresses extends LocalStorageBacked {
  constructor() {
    super();
    // The Local Storage may return null, so in this case we must ignore the null value
    if (!this.cached_value_) this.cached_value_ = {};
  }

  /** The storage_name getter
   * @override
   * @return {string} */
  get storage_name() {
    return "BookedAddresses";
  }

  /** The value setter
   * @param {Array<string>} booked_addresses
   */
  set value(booked_addresses) {
    /** @type {Object} */
    let tmp_value = {};
    // Remove duplicate values
    booked_addresses.map((booked_address)=>{
      Object.defineProperty(tmp_value, booked_address.toString(), {
        enumerable: true,
        writable: true,
        configurable: true,
        value: null
      });
    });
    this.cached_value_ = tmp_value;
    window.localStorage.setItem(this.storage_name, JSON.stringify(this.cached_value_));
  }

  /** The value getter
   * @return {Array<string>} */
  get value() {
    return Object.keys(this.cached_value_);
  }

  /** Add the item to the storage
   * @param {string} */
  add(item) {
    Object.defineProperty(this.cached_value_, item.toString(), {
      enumerable: true,
      writable: true,
      configurable: true,
      value: null
    });
    // Update the Local Storage using setter and getter
    this.value = this.value;
  }

  /** Remove the item from the storage
   * @param {string} */
  remove(item) {
    delete this.cached_value_.item;
    // Update the Local Storage using setter and getter
    this.value = this.value;
  }
}

/** The array of schedule items
 * @extends LocalStorageBacked
 */
export class ScheduleTable extends LocalStorageBacked {
  constructor() {
    super();
    // The Local Storage may return null, so in this case we must ignore the null value
    if (!this.cached_value_) this.cached_value_ = [];
  }

  /** The storage_name getter
   * @override
   * @return {string} */
  get storage_name() {
    return "ScheduleTable";
  }

  /** The value setter
   * @param {Array<ScheduleItem>} schedule_table
   */
  set value(schedule_table) {
    this.cached_value_ = schedule_table;
    window.localStorage.setItem(this.storage_name, JSON.stringify(this.cached_value_));
  }

  /** The value getter
   * @return {Array<ScheduleItem>} */
  get value() {
    return this.cached_value_;
  }

  /** Add the item to the storage
   * @param {ScheduleItem} */
  add(item) {
    this.cached_value_ = this.cached_value_.concat(item);
    // Update the Local Storage using setter and getter
    this.value = this.value;
  }

  /** Remove the item from the storage
   * @param {ScheduleItem} */
  remove(item) {
    for (var i = 0; i < this.cached_value_.length; i++) {
      if (this.cached_value_[i] === item) {
        if (this.cached_value_.length == 1) {
          this.cached_value_ = [];
        } else {
          this.cached_value_ =
            this.cached_value_.slice(0, i).concat(this.cached_value_.slice(i+1));
        }
        break;
      }
    }
    // Update the Local Storage using setter and getter
    this.value = this.value;
  }
}

/** A Web3.Contract instance deployed from an object,
 *  which is stored as stringified in the Local Storage
 * @extends LocalStorageBacked
 */
export class ContractInstance extends LocalStorageBacked {
  constructor() {
    super();
    if (!window.ethereum) return;
    /** @const {Object} */
    this.web3_instance_ = new Web3(window.ethereum);
    if (this.cached_value_) {
      // Create a new Web3.Contract instance based on the stored contract_instance
      this.cached_value_ =
        new this.web3_instance_.eth.Contract(this.cached_value_._jsonInterface, this.cached_value_._address);
    }
  }

  /** The storage_name getter
   * @override
   * @return {string} */
  get storage_name() {
    return "ContractInstance";
  }

  /** Syncs the cached value with the storage and returns the synced value
   * @override
   * @return {?Object} */
  get storage_value() {
    super.storage_value();
    if (this.cached_value_) {
      /** Create a new Web3.Contract instance based on the stored contract_instance
       * @type {Object} */
      this.cached_value_ =
        new this.web3_instance_.eth.Contract(this.cached_value_._jsonInterface, this.cached_value_._address);
    }
    return this.cached_value_;
  }

  /** The value setter
   * @param {Object} contract_instance
   */
  set value(contract_instance) {
    window.localStorage.setItem(this.storage_name, JSON.stringify(contract_instance));
    // Create a new Web3.Contract instance based on the stored contract_instance
    this.cached_value_ =
      new this.web3_instance_.eth.Contract(contract_instance._jsonInterface, contract_instance._address);
  }

  /** The value getter
   * @return {Object} */
  get value() {
    return this.cached_value_;
  }
}
