import {LitElement, html, css, render as LitRender} from 'lit';
import {customElement, property, query} from 'lit/decorators.js';
import '@kor-ui/kor';
import Utils from '../utils.js';

/** @fileoverview The component for a blockchain switching with Metamask,
 * it consists of an icon-button, a tooltip and a modal window with a form to fill
 * Generates the ${name}-changed event when a property ${name} was changed
 * @externs
 * @module
 * @prop {boolean} has_logined Whether a user has logined
 * @prop {string} theme The "light" and "dark" enumeration
 * @prop {Element} invoke_button The button which opens an invoke form
 * @prop {Element} invoke_form The form to fill for the invoking of the feature
 * @private @prop {number} blockchain_index_ The index of a currently selected blockchain
 * @private @prop {Array<Object>} blockchains_data_ Different blockchains parameters
 */
@customElement('dapp-switch-chain')
export default class dappSwitchChain extends LitElement {
  @property({type: Boolean, reflect: true}) has_logined = false;
  @property({type: String, reflect: true}) theme = "light";
  @query("kor-icon", true) invoke_button;
  @query("kor-modal", false) invoke_form;
  /// Don't reflect these properties to HTML attributes (avoid a big data's duplication),
  /// don't update this component on these properties changes
  /// Private
  @property({type: Number, reflect: false, noAccessor: true, state: true}) blockchain_index_ = 0;
  @property({type: Array, reflect: false, noAccessor: true, state: true}) blockchains_data_ = [
    // The default chain switching doesn't work (Goerli is a default one),
    // see pls the https://github.com/MetaMask/metamask-extension/issues/10597
    {
      chainId: '0x5',
      chainName: 'Goerli-Testnet',
        nativeCurrency:
          {
            name: 'Ether',
            symbol: 'ETH',
            decimals: 18
          },
      rpcUrls: ['https://goerli.infura.io/v3/undefined'],
      blockExplorerUrls: ['https://goerli.etherscan.io'],
    },
    {
      chainId: '0x13881',
      chainName: 'Mumbai-Testnet (Polygon-Matic)',
        nativeCurrency:
          {
            name: 'MATIC',
            symbol: 'MATIC',
            decimals: 18
          },
      rpcUrls: ['https://rpc-mumbai.maticvigil.com'],
      blockExplorerUrls: ['https://explorer-mumbai.maticvigil.com'],
    },
    {
      chainId: '0x89',
      chainName: 'Polygon (Matic) Network',
        nativeCurrency:
          {
            name: 'MATIC',
            symbol: 'MATIC',
            decimals: 18
          },
      rpcUrls: ['https://rpc-mainnet.maticvigil.com'],
      blockExplorerUrls: ['https://explorer-mainnet.maticvigil.com'],
    },
  ];
  ///

  render() {
    return html`
      <kor-icon id="switchChain" icon="timeline" button style="display: none"
        @click="${(e)=>{
          this.invoke_form.setAttribute("visible", "true");
        }}"
      ></kor-icon>
      <kor-modal icon="timeline" label="Let's add and switch into a chain" theme="${this.theme}">
        <kor-card flat>
          <kor-grid columns="1" fit-content>
            <kor-card grid-cols="1" flat>
              <kor-input type="select" label="Select a chain to add and switch to in Metamask" icon="view_list">
                ${this.blockchains_data_.map(
                  (blockchain, index) => html`
                    <kor-menu-item label="${blockchain.chainName}"
                      @click="${(e)=>{
                        this.blockchain_index_ = index;
                      }}"
                    ></kor-menu-item>`
                )}
              </kor-input>
            </kor-card>
          </kor-grid>
        </kor-card>
        <kor-button label="Switch chain" color="Primary" slot="footer"
          @click="${(e)=>{
            this.handleSwitchChain();
          }}"
        ></kor-button>
      </kor-modal>`;
  }

  handleSwitchChain() {
    if (!window.ethereum) {
      console.log("Install Metamask please");
      return;
    }
    window.ethereum.request({
      method: 'wallet_addEthereumChain',
      params: [this.blockchains_data_[this.blockchain_index_]]
    }).then(() => {
      console.log("The chain switched");
    }, (error) => {
      console.log("The wallet_addEthereumChain error == ", error);
    });
  }

  constructor() {
    super();
  }

  connectedCallback() {
    super.connectedCallback()
  }

  attributeChangedCallback(name, oldval, newval) {
    super.attributeChangedCallback(name, oldval, newval);
    if (name == 'has_logined') {
      if (this.has_logined == true) {
        Utils.login_component(this);
        this.invoke_button.style.display = "block";
      } else {
        this.invoke_button.style.display = "none";
        Utils.logout_component(this);
      }
    }
    if (name == 'theme' && this.invoke_form) {
      this.invoke_form.theme = this.theme;
    }
    this.dispatchEvent(new Event(`${name}-changed`));
  }
}

