import {LitElement, html, css} from 'lit';
import {customElement, property, query} from 'lit/decorators.js';
import '@kor-ui/kor';
import Utils from '../utils.js';

/** @fileoverview The component for a smart contract method unsubscribe() invoking
 * Generates the "unsubscribed" event on a success
 * Generates the ${name}-changed event when a property ${name} was changed
 * @externs
 * @module
 * @prop {string} current_address The address, from which the contract's method will be invoked
 * @prop {number} subscription_id The subscription ID to unsubscribe
 * @prop {Element} invoke_button The button which opens a user form
 * @prop {Element} user_form The form to fill for the method invoking
 * @prop {Element} notification_container The container where to display notifications
 * @prop {Object} contract_instance The contract instance to invoke
 * @private @prop {number} gas_price_gwei_ A gas price for the current transaction
 * @private @prop {Array<(string|Object)>} errors_
 * @private @prop {Array<(string|Object)>} successes_
 */
@customElement('dapp-unsubscribe')
export default class dappUnsubscribe extends LitElement {
  @property({type: String, reflect: true}) current_address = null;
  @property({type: Number, reflect: true}) subscription_id = null;
  @query("kor-icon", true) invoke_button;
  @query("kor-notification-item", true) user_form;
  /// Don't reflect these properties to HTML attributes (avoid a big data's duplication),
  /// don't update this component on these properties changes
  @property({type: Object, reflect: false, noAccessor: true}) notification_container = null;
  @property({type: Object, reflect: false, noAccessor: true}) contract_instance = null;
  /// Private
  // TODO: Set up the gas_price_gwei_ in the Utils.setup_component() from user's preferences
  @property({type: Number, reflect: false, noAccessor: true, state: true}) gas_price_gwei_ = 10;
  @property({type: Array, reflect: false, noAccessor: true, state: true}) errors_ = [];
  @property({type: Array, reflect: false, noAccessor: true, state: true}) successes_ = [];
  ///

  render() {
    return html`
      <kor-icon icon="delete" button
        @click="${(e)=>{
          this.user_form.setAttribute("visible", "true");
        }}"
      ></kor-icon>
      <kor-notifications>
        <kor-notification-item icon="warning" label="Unsubscribe" sticky>
          <kor-text size="header-2">Are you sure you want to unsubscribe?</kor-text>
          <kor-button slot="footer" color="secondary" label="No"
            @click="${(e)=>{
              this.user_form.removeAttribute("visible");
            }}"
          ></kor-button>
          <kor-button slot="footer" color="secondary" label="Yes"
            @click="${(e)=>{
              this.user_form.removeAttribute("visible");
              this.unsubscribe();
            }}"
          ></kor-button>
        </kor-notification-item>
      </kor-notifications>`;
  }

  constructor() {
    super();
  }

  connectedCallback() {
    super.connectedCallback()
    Utils.setup_component(this);
    Utils.login_component(this);
  }

  unsubscribe() {
    if (!this.contract_instance) {
      this.errors_ = this.errors_.concat("Please set a contract instance into this component");
      Utils.error_notifications(this.errors_, this.notification_container);
      return;
    }
    /** @const {Object} */
    const transaction_object =
      this.contract_instance.methods.unsubscribe(this.subscription_id);
    console.log("The transaction_object == ", transaction_object);
    console.log("The subscription ID == ", this.subscription_id);
    Utils.send_transaction(
      transaction_object,
      this.current_address, this.gas_price_gwei_, 0 /* a value to send with */,
      this.errors_, this.successes_, this.notification_container
    ).then((receipt)=>{
      this.successes_ = this.successes_.concat(JSON.stringify(receipt));
      Utils.success_notifications(this.successes_, this.notification_container);
      console.log(receipt);
      this.dispatchEvent(new Event('unsubscribed',
        {bubbles: true, composed: true}));
      this.errors_ = [];
      this.successes_ = [];
    });
  }

  attributeChangedCallback(name, oldval, newval) {
    super.attributeChangedCallback(name, oldval, newval);
    this.dispatchEvent(new Event(`${name}-changed`));
  }
}
