/** @fileoverview Dynamic GUI definition for transactions
 *  @externs
 *  @module
 *  @see module:../type_def.h
 *  @see module:../local_storage
 */
import '@kor-ui/kor';
import '@kor-ui/kor/kor-styles.css';
import {html} from 'lit';

export default {
  /** Generates a transactions view
   *
   * @callback transaction_details_callback
   * @param {Object} transaction
   *
   * @func transactions
   * @param {Array<Object>} in_transactions
   * @param {Array<Object>} out_transactions
   * @param {transaction_details_callback} transaction_details
   * @return {Object}
   */
  transactions(in_transactions, out_transactions, transaction_details) {
    return html`
      <kor-grid columns="2">
        <kor-card grid-cols="1" grid-cols-m="2" grid-cols-s="2" flat>
          <kor-text size="header-1">Incoming transactions</kor-text>
          <kor-table columns="1fr auto">
            <kor-table-row slot="header">
              <kor-table-cell head>From address</kor-table-cell>
              <kor-table-cell head>Amount in ETH</kor-table-cell>
            </kor-table-row>
            ${in_transactions.map(
              (transaction) => html`
                <kor-table-row @click="${(e)=>{transaction_details(transaction)}}">
                  <kor-table-cell>${transaction.attributes.from_address}</kor-table-cell>
                  <kor-table-cell>${(Number.parseInt(transaction.attributes.value)/Math.pow(10,18)).toFixed(18)}</kor-table-cell>
                </kor-table-row>`
            )}
          </kor-table>
        </kor-card>
        <kor-card grid-cols="1" grid-cols-m="2" grid-cols-s="2" flat>
          <kor-text size="header-1">Outgoing transactions</kor-text>
          <kor-table columns="1fr auto">
            <kor-table-row slot="header">
              <kor-table-cell head>To address</kor-table-cell>
              <kor-table-cell head>Amount in ETH</kor-table-cell>
            </kor-table-row>
            ${out_transactions.map(
              (transaction) => html`
                <kor-table-row @click="${(e)=>{transaction_details(transaction)}}">
                  <kor-table-cell>${transaction.attributes.to_address}</kor-table-cell>
                  <kor-table-cell>${(Number.parseInt(transaction.attributes.value)/Math.pow(10,18)).toFixed(18)}</kor-table-cell>
                </kor-table-row>`
            )}
          </kor-table>
        </kor-card>
      </kor-grid>`;
  },
  /** Generates a view to add a new transaction
   *  It is possible to choose whether to use a destination address
   *  from own addresses or from booked addresses or to enter a destination address manually
   * @param {number} account_balance The source address balance in GWEI
   * @param {?Array<string>} own_addresses The list of own addresses
   * @param {?BookedAddresses} booked_addresses The list of booked addresses as an instance of the BookedAddresses class backed by the Local Storage
   * @param {TransactionData} transaction_data The reference to an object, where to store the results and get a defaults
   * @param {boolean} has_destination Whether the transaction has an explicit destination
   * @return {Object}
   */
  add_transaction(account_balance, own_addresses, booked_addresses, transaction_data, has_destination)
  {
    return html`
      ${has_destination ? html`
        <kor-radio-button label="Choose from own addresses"
          @active-changed="${(e)=>{
            /** @const {Element} */
            const own_addresses_input =
              e.target.parentElement.querySelectorAll('kor-input')[0];
            if (e.target.active) {
              own_addresses_input.style.display = "inherit";
              transaction_data.destination_address = own_addresses_input.value;
            } else {
              own_addresses_input.style.display = "none";
            }
          }}">
        </kor-radio-button>
        <kor-input type="select" label="The own address will be used as destination" icon="view_list" style="display: none"
          @active-changed="${(e)=>{
            if (e.target.active) transaction_data.destination_address = e.target.value;
          }}"
        >
          ${own_addresses.map(
            (own_address) => html`
              <kor-menu-item label="${own_address}"></kor-menu-item>`
          )}
        </kor-input>
        <kor-radio-button label="Choose from booked addresses"
          @active-changed="${(e)=>{
            /** @const {Element} */
            const booked_addresses_input =
              e.target.parentElement.querySelectorAll('kor-input')[1];
            if (e.target.active) {
              booked_addresses_input.style.display = "inherit";
              transaction_data.destination_address = booked_addresses_input.value;
            } else {
              booked_addresses_input.style.display = "none";
            }
          }}">
        </kor-radio-button>
        <kor-input type="select" label="The booked address will be used as destination" icon="view_list" style="display: none"
          @active-changed="${(e)=>{
            if (e.target.active) transaction_data.destination_address = e.target.value;
          }}"
        >
          ${booked_addresses.value.map(
            (booked_address) => html`
              <kor-menu-item label="${booked_address}"></kor-menu-item>`
          )}
        </kor-input>
        <kor-radio-button label="Enter a new destination address" active
          @active-changed="${(e)=>{
            /** @const {Element} */
            const entered_address_input =
              e.target.parentElement.querySelectorAll('kor-input')[2];
            if (e.target.active) {
              entered_address_input.style.display = "inherit";
              transaction_data.destination_address = entered_address_input.value;
            } else {
              entered_address_input.style.display = "none";
            }
          }}">
        </kor-radio-button>
        <kor-input label="The destination address:" type="text" value="" style="display: inherit"
          @active-changed="${(e)=>{
            if (!e.target.active) transaction_data.destination_address = e.target.value;
          }}">
        </kor-input>`
      : ''}
      <kor-input label="The transaction's amount in GWEI:"
        type="number" value="0" min=0 max=${account_balance}
        @active-changed="${(e)=>{
          if (!e.target.active) transaction_data.amount_gwei = e.target.value;
        }}">
      </kor-input>
      <kor-input label="The transaction's gas price in GWEI:"
        type="number" value="${transaction_data.gas_price_gwei}" min=0
        @active-changed="${(e)=>{
          if (!e.target.active) transaction_data.gas_price_gwei = e.target.value;
        }}">
      </kor-input>
      <kor-input label="The transaction's gas limit:"
        type="number" value="${transaction_data.gas_limit}" min=0
        @active-changed="${(e)=>{
          if (!e.target.active) transaction_data.gas_limit = e.target.value;
        }}">
      </kor-input>`;
  }
}
