import {LitElement, html, css, render as LitRender} from 'lit';
import {customElement, property, query} from 'lit/decorators.js';
import '@kor-ui/kor';
import AppTemplates from '../templates/app.js';
import SubscriptionsTemplates from '../templates/subscriptions.js';
import Utils from '../utils.js';
import { sharedStyles } from '../shared_styles.js';
import './subscribe.js';

/** @fileoverview The component for subscriptions managing
 * Generates the ${name}-changed event when a property ${name} was changed
 * @externs
 * @module
 * @prop {boolean} visible Whether this component is visible
 * @prop {boolean} has_logined Whether a user has logined
 * @prop {string} theme The "light" and "dark" enumeration
 * @prop {string} current_address The address, from which the transaction will be inspected
 * @prop {Element} subscriptions_tools Tools container
 * @prop {Element} subscriptions_content Subscriptions table container
 * @prop {Element} subscription_details_modal The modal window to show details of a particular transaction
 * @prop {Element} subscription_details_content The container with details of a particular transaction in the modal window
 * @prop {Element} subscribe
 * @prop {Object} contract_instance The contract instance to invoke
 * @private @prop {Array<Array<string>>} subscriptions_
 * @see module:../type_def.h
 */
@customElement('dapp-subscriptions')
export default class dappSubscriptions extends LitElement {
  @property({type: Boolean, reflect: true}) visible = false;
  @property({type: Boolean, reflect: true}) has_logined = false;
  @property({type: String, reflect: true}) theme = "light";
  @property({type: String, reflect: true}) current_address = null;
  @query("#subscriptionsTools", false) subscriptions_tools;
  @query("#subscriptionsContent", false) subscriptions_content;
  @query("#subscriptionDetailsModal", false) subscription_details_modal;
  @query("#subscriptionDetailsContent", true) subscription_details_content;
  @query("dapp-subscribe", true) subscribe;
  /// Don't reflect these properties to HTML attributes (avoid a big data's duplication),
  /// don't update this component on these properties changes
  @property({type: Object, reflect: false, noAccessor: true}) contract_instance = null;
  /// Private
  @property({type: Object, reflect: false, noAccessor: true, state: true}) subscriptions_ = [];
  ///

  static get styles() {
    return [sharedStyles,
            css``];
  }

  render() {
    return html`
      <div id="subscriptionsTools" grid-rows="1" style="display: none">
        <kor-icon class="heading_icon" icon="account_balance_wallet" color="var(--text-1)" size="m"></kor-icon>
        <kor-card class="heading_tools" flat>
          <dapp-subscribe
            @subscription-created="${(e)=>{
              this.getSubscriptions();
              console.log("The subscription created");
            }}"
          ></dapp-subscribe>
        </kor-card>
      </div>
      <div id="subscriptionsContent" grid-rows="9" style="display: none"></div>
      <kor-modal id="subscriptionDetailsModal" icon="list" label="The subscription's details" theme="light">
        <kor-card id="subscriptionDetailsContent" flat></kor-card>
      </kor-modal>`;
  }

  /** Renders the
   * @func subscription_details details of a particular subscription in the modal window
   * @param {Array<string>} subscription */
  subscription_details(subscription) {
    /** @const @struct {Object} */
    const sub_details = {
      subscriptionID: subscription[0],
      isActive: subscription[1],
      isPendingCancel: subscription[2],
      subscriberAddress: subscription[3],
      vendorAddress: subscription[4],
      productID: subscription[5],
      amount: subscription[6],
      billingInterval: subscription[7], // In seconds
      lastPaymentTime: subscription[8]  // UNIX timestamp (equals to startTime on the first payment)
    };
    LitRender(
      AppTemplates.object_entries_two_columns(Object.entries(sub_details)),
      this.subscription_details_content);
    this.subscription_details_modal.setAttribute("visible", "true");
  }

  /** @func getSubscriptions - async */
  async getSubscriptions() {
    /** @const {Array<string>} */
    const subscriber_subscription_IDs =
      await this.contract_instance.methods.getSubscriberSubscriptionIDs()
        .call({from: this.current_address});
    this.subscriptions_ = [];
    /** @const {Array<Promise>} */
    const promises =
      subscriber_subscription_IDs.map((subscription_ID)=>{
        return this.contract_instance.methods.getSubscription(subscription_ID)
          .call({from: this.current_address})
          .then((subscription)=>{this.subscriptions_ = this.subscriptions_.concat([subscription])});
      });
    /// Invoke the code below after all subscriptions are fetched
    Promise.all(promises).then(() => {
      console.log("this.subscriptions_ == ", this.subscriptions_);
      LitRender(
        SubscriptionsTemplates.subscriptions(
          this.subscriptions_, (subscription)=>{this.subscription_details(subscription)},
          ()=>{this.getSubscriptions()}),
        this.subscriptions_content);
      this.requestUpdate();
    });
  }

  constructor() {
    super();
  }

  connectedCallback() {
    super.connectedCallback();
    if (!this.style) this.style="display: none;";
    Utils.setup_component(this);
  }

  attributeChangedCallback(name, oldval, newval) {
    super.attributeChangedCallback(name, oldval, newval);
    if (name == 'has_logined') {
      if (this.has_logined == true) {
        Utils.login_component(this);
        this.getSubscriptions();
      } else {
        Utils.logout_component(this);
        LitRender(html``, this.subscriptions_content);
        this.subscriber_subscription_IDs_ = [];
        this.subscriptions_ = [];
      }
      this.subscribe.has_logined = this.has_logined;
    }
    if (name == 'visible') {
      this.style.display = "block";
      if (this.visible == true) {
        if (this.has_logined == true) {
          this.subscriptions_tools.style.display = "block";
          this.subscriptions_content.style.display = "block";
        } else {
          this.subscriptions_tools.style.display = "block";
          this.subscriptions_content.style.display = "none";
        }
      } else {
        this.subscriptions_tools.style.display = "none";
        this.subscriptions_content.style.display = "none";
        this.style.display = "none";
      }
    }
    if (name == 'theme' && this.subscription_details_modal) {
      this.subscription_details_modal.theme = this.theme;
    }
    this.dispatchEvent(new Event(`${name}-changed`));
  }
}
