import {LitElement, html, css, render as LitRender} from 'lit';
import {customElement, property, query} from 'lit/decorators.js';
import '@kor-ui/kor';
import TransactionsTemplates from '../templates/transactions.js';
import ScheduleTemplates from '../templates/schedule.js';
import {ScheduleTable} from '../local_storage.js';
import Utils from '../utils.js';
import { sharedStyles } from '../shared_styles.js';

/** @fileoverview The component for a schedule managing
 * Generates the ${name}-changed event when a property ${name} was changed
 * @externs
 * @module
 * @prop {boolean} visible Whether this component is visible
 * @prop {boolean} has_logined Whether a user has logined
 * @prop {string} theme The "light" and "dark" enumeration
 * @prop {string} current_address The address, from which the transaction will be sent
 * @prop {Element} schedule_tools Tools container
 * @prop {Element} schedule_details Schedule table container
 * @prop {Element} add_schedule_form The "Add a payment" form container
 * @prop {Element} notification_container This component notification container
 * @prop {Element} transaction_content The dynamically generated transaction content container
 * @prop {Element} schedule_content The dynamically generated schedule content container
 * @prop {BookedAddresses} booked_addresses The addresses already used to make transactions as an instance of the BookedAddresses class backed by the Local Storage
 * @prop {Web3Wrappers} web3_wrappers The object to represent different web3 wrappers
 * @prop {ScheduleTable} schedule_table The object, which stores the table of schedule items as an instance of the ScheduleTable class backed by the Local Storage
 * @private @prop {TransactionData} transaction_data_ The transaction's details temporary object
 * @private @prop {ScheduleData} schedule_data_ The schedule's details temporary object
 * @see module:../type_def.h
 */
@customElement('dapp-schedule')
export default class dappSchedule extends LitElement {
  @property({type: Boolean, reflect: true}) visible = false;
  @property({type: Boolean, reflect: true}) has_logined = false;
  @property({type: String, reflect: true}) theme = "light";
  @property({type: String, reflect: true}) current_address = null;
  @query("#scheduleTools", false) schedule_tools;
  @query("#scheduleDetails", false) schedule_details;
  @query("#addScheduleForm", false) add_schedule_form;
  @query("#notificationContainer", true) notification_container;
  @query("#transactionContent", true) transaction_content;
  @query("#scheduleContent", true) schedule_content;
  /// Don't reflect these properties to HTML attributes (avoid a big data's duplication),
  @property({type: Object, reflect: false}) booked_addresses = null;
  /// don't update this component automatically on these properties changes
  @property({type: Object, reflect: false, noAccessor: true}) web3_wrappers = null;
  @property({type: Object, reflect: false, noAccessor: true}) schedule_table = new ScheduleTable();
  /// Private
  @property({type: Object, reflect: false, noAccessor: true, state: true}) transaction_data_ = {
    gas_price: 1,
    gas_limit: 21000
  };
  @property({type: Object, reflect: false, noAccessor: true, state: true}) schedule_data_ = {};
  ///

  static get styles() {
    return
      [sharedStyles,
       css`
         input[type="date"] {
           background: transparent;
           color: white;
         }
         input[type="date"]::-webkit-calendar-picker-indicator {
           filter: invert(100%);
         }`
      ];
  }

  render() {
    return html`
      <div id="scheduleTools" grid-rows="1" style="display: none">
        <kor-icon class="heading_icon" icon="schedule" color="var(--text-1)" size="m"></kor-icon>
        <kor-card class="heading_tools" flat>
          <kor-button label="Add a payment" color="Primary"
            @click="${(e)=>{
              this.web3_wrappers.metamask.eth.getBalance(this.current_address).then((account_balance)=>{
                LitRender(TransactionsTemplates.add_transaction(
                           account_balance, [] /* TODO: own_addresses */,
                           this.booked_addresses, this.transaction_data_, true /* has_destination */),
                           this.transaction_content);
                LitRender(ScheduleTemplates.add_schedule(
                  this.schedule_data_), this.schedule_content);
                this.add_schedule_form.setAttribute("visible", "true");
              });
            }}"
          ></kor-button>
        </kor-card>
      </div>
      <div id="scheduleDetails" grid-rows="9" style="display: none"></div>
      <kor-modal id="addScheduleForm" icon="today" label="Add a recurrent payment" theme="light"
        @visible-changed="${(e)=>{
          if (!e.target.visible) {
            /** @const {?Element} */
            const notification_item =
              this.notification_container.querySelector('kor-notification-item');
            if (notification_item) notification_item.removeAttribute("visible");
          }
        }}"
      >
        <div id="notificationContainer"></div>
        <kor-card flat>
          <kor-grid columns="1" fit-content>
            <kor-card id="transactionContent" grid-cols="1" flat></kor-card>
            <kor-card id="scheduleContent" grid-cols="1" flat></kor-card>
          </kor-grid>
        </kor-card>
        <kor-button label="Add" color="Primary" slot="footer"
          @click="${(e)=>{
            this.handleAddSchedule();
          }}"
        ></kor-button>
      </kor-modal>`;
  }

  refresh_schedule() {
    LitRender(
      ScheduleTemplates.schedule_table(this.schedule_table, ()=>{this.refresh_schedule()}),
      this.schedule_details);
  }

  handleAddSchedule() {
    if (!Utils.check_transaction_data(
      this.transaction_data_, this.notification_container, true /* has_destination */)) return;
    if (!Utils.check_schedule_data(this.schedule_data_, this.notification_container)) return;
    /** @const {ScheduleItem} */
    const schedule_item = {
      transaction_data: this.transaction_data_,
      schedule_data: this.schedule_data_
    };
    this.schedule_table.add(schedule_item);
    this.add_schedule_form.removeAttribute("visible");
    LitRender(
      ScheduleTemplates.schedule_table(this.schedule_table, ()=>{this.refresh_schedule()}),
      this.schedule_details);
    // Create new objects by copying of inner values
    /** @type {Object} */
    let tmp_data = {};
    Object.assign(tmp_data, this.transaction_data_)
    this.transaction_data_ = tmp_data;
    tmp_data = {};
    Object.assign(tmp_data, this.schedule_data_);
    this.schedule_data_ = tmp_data;
  }

  constructor() {
    super();
  }

  connectedCallback() {
    super.connectedCallback();
    if (!this.style) this.style="display: none;";
    /** @const {NodeList} */
    const undefinedElements =
      this.shadowRoot.querySelectorAll(':not(:defined)');
    /** @const {Array<Promise>} */
    const promises = [...undefinedElements].map(element => {
      return customElements.whenDefined(element.localName);
    });
    /// Invoke the code below after this.shadowRoot is rendered
    Promise.all(promises).then(() => {
      Utils.setup_component(this);
      this.refresh_schedule();
    });
  }

  attributeChangedCallback(name, oldval, newval) {
    super.attributeChangedCallback(name, oldval, newval);
    if (name == 'has_logined') {
      if (this.has_logined == true) {
        Utils.login_component(this);
      } else {
        Utils.logout_component(this);
      }
    }
    if (name == 'visible' && this.schedule_tools && this.schedule_details)
    {
      if (this.visible == true) {
        this.style.display = "block";
        if (this.has_logined == true) {
          this.schedule_tools.style.display = "block";
          this.schedule_details.style.display = "block";
        } else {
          this.schedule_tools.style.display = "none";
          this.schedule_details.style.display = "block";
        }
      } else {
        this.schedule_tools.style.display = "none";
        this.schedule_details.style.display = "none";
        this.style.display = "none";
      }
    }
    if (name == 'theme' && this.add_schedule_form) {
      this.add_schedule_form.theme = this.theme;
    }
    this.dispatchEvent(new Event(`${name}-changed`));
  }
}
