/** @fileoverview Various utilities and helpers
 *  @externs
 *  @module
 *  @see module:type_def.h
 *  @see module:templates/app
 *  @see module:local_storage.js
 */
import {render} from 'lit';
import AppTemplates from './templates/app.js';
import {BookedAddresses, ContractInstance} from './local_storage.js';

export default {
  /** Checks whether the object is properly filled by a user
   * and shows the notification if some field is not filled
   *
   * @func check_transaction_data
   * @param {TransactionData} transaction_data The object to check
   * @param {Element} notification_container The notification container
   * @param {boolean} has_destination Whether the transaction has an explicit destination
   * @return {boolean} Whether the object has passed the check
   */
  check_transaction_data(transaction_data, notification_container, has_destination) {
    /** @type {Array<string>} */
    let errors = [];
    if (has_destination) {
      if (!transaction_data.destination_address) {
        errors = errors.concat("The destination address is not filled");
      } else if (!/0x[0-9a-f]{40}/.test(transaction_data.destination_address)) {
        errors = errors.concat("The destination address has wrong format");
      }
    }
    if (!transaction_data.amount_gwei || transaction_data.amount_gwei <= 0) {
      errors = errors.concat("The amount is not filled");
    }
    if (!transaction_data.gas_price_gwei) {
      errors = errors.concat("The gas price is not filled");
    }
    if (!transaction_data.gas_limit) {
      errors = errors.concat("The gas limit is not filled");
    }
    if (errors.length) {
      render(AppTemplates.error_notifications(errors), notification_container);
      /** @const {Element} */
      const notification_item =
        notification_container.querySelector('kor-notification-item');
      notification_item.setAttribute("visible", "true");
    }
    return !errors.length;
  },
  /** Checks whether the object is properly filled by a user
   * and shows the notification if some field is not filled
   *
   * @func check_schedule_data
   * @param {ScheduleData} schedule_data
   * @param {Element} notification_container The notification container
   * @return {boolean} Whether the object has passed the check
   */
  check_schedule_data(schedule_data, notification_container) {
    /** @type {Array<string>} */
    let errors = [];
    if (!schedule_data.start_date) {
      errors = errors.concat("The start date is not filled");
    }
    if (!schedule_data.start_time) {
      errors = errors.concat("The start time is not filled");
    }
    if (errors.length) {
      render(AppTemplates.error_notifications(errors), notification_container);
      /** @const {Element} */
      const notification_item =
        notification_container.querySelector('kor-notification-item');
      notification_item.setAttribute("visible", "true");
    }
    return !errors.length;
  },
  /** Converts start_time in minutes into the Clock object
   *
   * @func start_time_to_clock
   * @param {number} start_time The time in minutes since the day started
   * @return {Clock}
   */
  start_time_to_clock(start_time) {
    /** @type {number} */
    let hour = Math.trunc(start_time / 60);
    /** @const {number} */
    const minute = start_time % 60;
    /** @const {string} */
    let am_pm = "AM";
    if (hour >= 12) {
      am_pm = "PM";
      hour -= 12;
    }
    if (hour.toFixed(0) == "0") hour = 12;
    return {
      hour: hour,
      minute: minute,
      am_pm: am_pm
    };
  },
  /** Shows a notification about errors
   * @func errors_notification
   * @param {Array<string>} errors
   * @param {Element} notification_container
   */
  error_notifications(errors, notification_container) {
    render(AppTemplates.error_notifications(errors), notification_container);
    /** @const {Element} */
    const notification_item =
      notification_container.querySelector('kor-notification-item');
    notification_item.setAttribute("visible", "true");
  },
  /** Shows a notification about successes
   * @func errors_notification
   * @param {Array<string>} successes
   * @param {Element} notification_container
   */
  success_notifications(successes, notification_container) {
    render(AppTemplates.success_notifications(successes), notification_container);
    /** @const {Element} */
    const notification_item =
      notification_container.querySelector('kor-notification-item');
    notification_item.setAttribute("visible", "true");
  },
  /** Asynchronously gets a current address from a blockchain provider
   * @func get_current_address
   * @return {?string} The current address
   */
  async get_current_address() {
    if (!window.ethereum) return null; // The blockchain provider is not set
    /** The example of using a chain provider's RPC without a Web3.js shim
     *  The Web3.js provides for that this statement: web3.eth.getAccounts().then((array_of_accounts)=>{});
     * @const {?Object<Array<string>>} */
    const accounts = await window.ethereum.request({method: 'eth_requestAccounts'});
    return Object.entries(accounts)[0][1];
  },
  /** Sends a transaction into a current blockchain
   * @func send_transaction
   * @param {Object} transaction_object
   * @param {string} address_from
   * @param {number} gas_price_gwei
   * @param {number} value_gwei
   * @param {Array<?(Object|string)>} errors
   * @param {Array<?(Object|string)>} successes
   * @param {Element} notification_container
   * @return {?Object} Returns a promise which resolves after the transaction is complete
   */
  send_transaction(transaction_object, address_from, gas_price_gwei, value_gwei,
                   errors, successes, notification_container)
  {
    if (!transaction_object || !transaction_object.send) {
      errors = errors.concat("An appropriate transaction object is not set");
      this.error_notifications(errors, notification_container);
      return null;
    }
    console.log("The transaction will be sent from ", address_from);
    console.log("The transaction's gas price in gwei == ", parseInt(gas_price_gwei));
    console.log("The transaction's value in gwei == ", parseInt(value_gwei));
    return transaction_object
      .send({
        from: address_from,
        gasPrice: (parseInt(gas_price_gwei) * Math.pow(10,9)),
        value: (parseInt(value_gwei) * Math.pow(10,9))
      },
      (error, transaction_hash)=>{
        if (error)
          errors = errors.concat(Object.entries(error));
        if (errors.length) {
          errors = errors.concat("The transaction hash == " + transaction_hash.toString());
          this.error_notifications(errors, notification_container);
        }
      })
      .on('error', (error)=>{
        errors = errors.concat(Object.entries(error));
        if (errors.length) {
          this.error_notifications(errors, notification_container);
        }
      })
      .on('transactionHash', (transaction_hash)=>{
        successes = successes.concat("The transaction hash == " + transaction_hash.toString());
        this.success_notifications(successes, notification_container);
      })
      .on('receipt', (receipt)=>{
        successes = successes.concat("The contract address == " + receipt.contractAddress.toString());
        this.success_notifications(successes, notification_container);
      })
      .on('confirmation', (confirmation_number, receipt)=>{
        successes = successes.concat("The confirmation number == " + confirmation_number);
        successes = successes.concat(Object.entries(receipt));
      });
  },
  /** Initializes the Mtaic.js,
   *  which can be done without errors from the Ethereum and Goerli chains only
   * @func init_matic
   * @param {Web3Wrappers} web3_wrappers
   * @param {string} current_address */
  init_matic(web3_wrappers, current_address) {
    // Create an SDK instance
    web3_wrappers.matic = new Matic({
      network: "testnet", // set network name
      version: "mumbai", // set network version
      // Set Matic provider - string or provider instance
      // Example: <network.Matic.RPC> OR new Web3.providers.HttpProvide(<network.Matic.RPC>)
      // Some flows like startExitFor[Metadata]MintableBurntToken, require a webSocket provider such as new web3 providers.WebsocketProvider('ws://localhost:8546')
      maticProvider: web3_wrappers.metamask,
      // Set Mainchain provider - string or provider instance
      // Example: 'https://ropsten.infura.io' OR new Web3.providers.HttpProvider('http://localhost:8545')
      parentProvider: web3_wrappers.metamask,
      // set default options e.g { from }
      parentDefaultOptions: {from: current_address},
      // set default options
      maticDefaultOptions: {from: current_address}
    });
    // Init matic
    web3_wrappers.matic.initialize().then(async () => {
      console.log("web3_wrappers.matic == ", web3_wrappers.matic);
    });
  },
  /** Creates the instances of Metamask and Matic APIs,
   *  invokes a login into Metamask wallet for the first time it is invoked
   * @func init_web3_wrappers
   * @param {Web3Wrappers} web3_wrappers
   * @param {string} current_address */
  init_web3_wrappers(web3_wrappers, current_address) {
    // Create a Web3 instance with a Metamask as the blockchain provider,
    // it is equal to the 'await Moralis.Web3.enable()' invocation,
    web3_wrappers.metamask = new Web3(window.ethereum);
    console.log("web3_wrappers.metamask == ", web3_wrappers.metamask);
    if (window.ethereum.networkVersion == 5) {
      // The current chain is Goerli
      this.init_matic(web3_wrappers, current_address);
    } else {
      // Wait until user has not switched to Goerli
      window.ethereum.on('chainChanged', (chainId) => {
        // The Matic.js on Testnet Mumbai may be inited from the Goerli chain only
        if (chainId == 5) this.init_matic(web3_wrappers, current_address);
      });
    }
  },
  /** Sets up component properties, which are not login dependent,
   *  if they are not set explicitly:
   *  - checks the super component for the property;
   *  - if a property is not set in the super component,
   *    then creates a new instance of the property or uses a default value
   * @func setup_component
   * @param {Element} component */
  setup_component(component) {
    const super_component = document.querySelector('dapp-main');
    if (!super_component) {
      console.log("setup_component: The super component was not found");
    }
    if (!component.theme && typeof component.theme !== "undefined") {
      if (super_component) component.theme = super_component.theme;
      if (!component.theme) {
        console.log("The theme property was not found in the super component");
        component.theme = "light";
      }
    }
    if (!component.booked_addresses && typeof component.booked_addresses !== "undefined") {
      if (super_component) component.booked_addresses = super_component.booked_addresses;
      if (!component.booked_addresses) {
        console.log("The booked_addresses property was not found in the super component");
        component.booked_addresses = new BookedAddresses();
      }
    }
    if (!component.notification_container && typeof component.notification_container !== "undefined") {
      if (super_component) component.notification_container = super_component.page_notification;
      if (!component.notification_container) {
        console.log("The notification_container property was not found in the super component");
        component.notification_container = document.createElement('div'); // > /dev/null
      }
    }
  },
  /** Sets up component properties, which are login dependent,
   *  if they are not set explicitly:
   *  - checks the super component for the property;
   *  - if a property is not set in the super component,
   *    then creates a new instance of the property or uses a default value
   * @func setup_component
   * @param {Element} component */
  login_component(component) {
    const super_component = document.querySelector('dapp-main');
    if (!super_component) {
      console.log("login_component: The super component was not found");
    }
    if (!component.current_address && typeof component.current_address !== "undefined") {
      if (super_component) component.current_address = super_component.current_address;
      if (!component.current_address) {
        console.log("The current_address property was not found in the super component");
        this.get_current_address().then((current_address)=>{
          component.current_address = current_address;
        });
      }
    }
    if (!component.web3_wrappers && typeof component.web3_wrappers !== "undefined") {
      if (super_component) component.web3_wrappers = super_component.web3_wrappers;
      if (!component.web3_wrappers) {
        console.log("The web3_wrappers property was not found in the super component");
        component.web3_wrappers = {};
        if (component.current_address) {
          this.init_web3_wrappers(component.web3_wrappers, component.current_address);
        } else {
          this.get_current_address().then((current_address)=>{
            this.init_web3_wrappers(component.web3_wrappers, current_address);
          });
        }
      }
    }
    if (!component.contract_instance && typeof component.contract_instance !== "undefined") {
      if (super_component) component.contract_instance = super_component.contract_instance.value;
      if (!component.contract_instance) {
        console.log("The contract_instance property was not found in the super component");
        component.contract_instance = new ContractInstance();
      }
    }
    if (!component.current_user && typeof component.current_user !== "undefined") {
      if (super_component) component.current_user = super_component.current_user;
      if (!component.current_user) {
        console.log("The current_user property was not found in the super component");
        component.current_user = {};
      }
    }
  },
  /** Clears component properties, which are login dependent, on logout
   * @func setup_component
   * @param {Element} component */
  logout_component(component) {
    component.current_address = null;
    component.web3_wrappers = null;
    component.contract_instance = null;
    component.current_user = null;
  }
}
