import {LitElement, html, css, render as LitRender} from 'lit';
import {customElement, property, query, queryAll} from 'lit/decorators.js';
import '@kor-ui/kor';
import '@kor-ui/kor/kor-styles.css';
import {BookedAddresses} from '../local_storage.js';
import LogInOut from '../login_logout.js';
import Utils from '../utils.js';
import './menu.js';
import './tab_strip.js';
import './summary.js';
import './subscriptions.js';
import './transactions.js';
import './schedule.js';
import './add_transaction.js';
import './deposit_ethers.js';
import './switch_chain.js';
import './deploy_contract.js';
import './compile_deploy_contract.js';
import './create_subscription_product.js';

/** @fileoverview The main GUI component of the DApp
 * Generates the ${name}-changed event when a property ${name} was changed
 * @see module:type_def.h
 * @externs
 * @module
 * @prop {boolean} has_logined Whether a user has logined
 * @prop {string} theme The "light" and "dark" enumeration
 * @prop {string} current_address The address, from which the transaction will be inspected
 * @prop {Object} current_user The current user value
 * @prop {ContractInstance} contract_instance The smart contract instance object backed by the Local Storage
 * @prop {Element} page
 * @prop {Element} menu
 * @prop {Element} tab_strip
 * @prop {Element} page_notification
 * @prop {Element} summary
 * @prop {Element} subscriptions
 * @prop {Element} transactions
 * @prop {Element} schedule
 * @prop {Element} add_transaction
 * @prop {Element} switch_chain
 * @prop {Element} deposit_ethers
 * @prop {Element} deploy_contract
 * @prop {Element} compile_deploy_contract
 * @prop {Element} create_subscription_product
 * @prop {NodeList} undefined_elements The undefined elements of the shadowRoot
 * @prop {BookedAddresses} booked_addresses The addresses already used to make transactions as an instance of the BookedAddresses class backed by the Local Storage
 * @prop {Web3Wrappers} web3_wrappers The object to represent different web3 wrappers
 * @private @prop {Function} handle_login_logout_
 * @see module:../type_def.h *
 */
@customElement('dapp-main')
export default class dappMain extends LitElement {
  @property({type: Boolean, reflect: true}) has_logined = false;
  @property({type: String, reflect: true}) theme = "light";
  @property({type: String, reflect: true}) current_address = null;
  /// Circular JSON structures, don't reflect
  @property({type: Object, reflect: false}) current_user = null;
  @property({type: Object, reflect: false}) contract_instance = null;
  ///
  @query("kor-page", false) page;
  @query("dapp-menu", true) menu;
  @query("dapp-tab-strip", true) tab_strip;
  @query("#pageNotification", true) page_notification;
  @query("dapp-summary", true) summary;
  @query("dapp-subscriptions", true) subscriptions;
  @query("dapp-transactions", true) transactions;
  @query("dapp-schedule", true) schedule;
  @query("dapp-add-transaction", true) add_transaction;
  @query("dapp-switch-chain", true) switch_chain;
  @query("dapp-deposit-ethers", true) deposit_ethers;
  @query("dapp-deploy-contract", true) deploy_contract;
  @query("dapp-compile-deploy-contract", true) compile_deploy_contract;
  @query("dapp-create-subscription-product", true) create_subscription_product;
  @queryAll(":not(:defined)", false) undefined_elements;
  /// Don't reflect these properties to HTML attributes (avoid a big data's duplication),
  /// don't update this component on these properties changes
  @property({type: Object, reflect: false, noAccessor: true}) booked_addresses = new BookedAddresses();
  @property({type: Object, reflect: false, noAccessor: true}) web3_wrappers = {};
  /// Private
  @property({type: Object, reflect: false, noAccessor: true, state: true}) handle_login_logout_ = null;
  ///

  render() {
    return html`
      <kor-page flat theme="${this.theme}" flex-direction="column">
        <div id="pageNotification"></div>
        <kor-app-bar slot="top" label="The crypto-subscriptions DApp" logo="https://webatix.github.io/test/xrp.svg">
          <dapp-add-transaction slot="functions"></dapp-add-transaction>
          <dapp-switch-chain slot="functions"></dapp-switch-chain>
          <dapp-deposit-ethers slot="functions"></dapp-deposit-ethers>
          <dapp-deploy-contract slot="functions"
            @contract-deployed="${(e)=>{
              console.log("The contract deployed, the contract_instance == ",
                e.target.contract_instance);
              // Update this component contract instance
              this.contract_instance.value = e.target.contract_instance;
              // Update all components which work with the contract instance
              this.create_subscription_product.contract_instance = this.contract_instance.value;
              this.subscriptions.subscribe.contract_instance = this.contract_instance.value;
            }}"
          ></dapp-deploy-contract>
          <dapp-compile-deploy-contract slot="functions"
            @contract-deployed="${(e)=>{
              console.log("The contract deployed, the contract_instance == ",
                e.target.contract_instance);
              // Update this component contract instance
              this.contract_instance.value = e.target.contract_instance;
              // Update all components which work with the contract instance
              this.create_subscription_product.contract_instance = this.contract_instance.value;
              this.subscriptions.subscribe.contract_instance = this.contract_instance.value;
            }}"
          ></dapp-compile-deploy-contract>
          <dapp-create-subscription-product slot="functions"
            @subscription-product-created="${(e)=>{
              console.log("The subscription product created with ID == ", e.target.productID);
            }}"
          ></dapp-create-subscription-product>
          <dapp-menu slot="functions"
            @has_logined-changed="${(e)=>{
              this.has_logined = e.target.has_logined;
            }}"
          ></dapp-menu>
        </kor-app-bar>
        <kor-grid columns="1" rows="10">
          <dapp-summary></dapp-summary>
          <dapp-subscriptions></dapp-subscriptions>
          <dapp-transactions></dapp-transactions>
          <dapp-schedule></dapp-schedule>
        </kor-grid>
        <kor-nav-bar slot="bottom">
          <dapp-tab-strip></dapp-tab-strip>
        </kor-nav-bar>
      </kor-page>`;
  }

  constructor() {
    super();
  }

  connectedCallback() {
    super.connectedCallback();
    /** @const {Array<Promise>} */
    const promises = [...this.undefined_elements].map(element => {
      return customElements.whenDefined(element.localName);
    });
    /// Invoke the code below after this.shadowRoot is rendered
    Promise.all(promises).then(() => {
      /// Check whether the dark theme should be applied
      if (window.matchMedia
          && window.matchMedia('(prefers-color-scheme: dark)').matches) {
        this.theme = "dark";
      }
      /// Set up tabs in the tab strip
      this.tab_strip.tabs = [
        {label: "Summary",
         component: this.summary},
        {label: "Subscriptions",
         component: this.subscriptions},
        {label: "Transactions",
         component: this.transactions},
        {label: "Schedule",
         component: this.schedule}
      ];
      this.handle_login_logout_ =
        LogInOut.Init(
          [this.summary, this.subscriptions, this.transactions, this.schedule,
           this.add_transaction, this.switch_chain, this.deposit_ethers,
           this.deploy_contract, this.compile_deploy_contract,
           this.create_subscription_product],
           this);
    }); // this.shadowRoot is rendered
  }

  attributeChangedCallback(name, oldval, newval) {
    super.attributeChangedCallback(name, oldval, newval);
    if (name == 'theme' && this.page) {
      this.page.theme = this.theme;
    }
    if (name == 'has_logined' && this.handle_login_logout_) {
      this.handle_login_logout_();
    }
    this.dispatchEvent(new Event(`${name}-changed`));
  }
}
