import {LitElement, html, css} from 'lit';
import {customElement, property, query} from 'lit/decorators.js';
import '@kor-ui/kor';
import Utils from '../utils.js';

/** @fileoverview Component for a smart contract compiling and deployment
 * Generates the contract-deployed event on a success deployment
 * After the success deployment the contract_instance property has the deployed contract instance
 * Generates the ${name}-changed event when a property ${name} was changed
 * The Web3.js must be added, the window.ethereum must have a blockchain provider
 * @externs
 * @module
 * @prop {boolean} has_logined Whether a user has logined
 * @prop {string} theme The "light" and "dark" enumeration
 * @prop {string} current_address The address, from which the contract will be deployed, the component tries to set up a current selected user's address from the blockchain provider as a default value
 * @prop {Element} open_button The button which opens a deployment form
 * @prop {Element} deployment_form The form to fill for the contract deployment
 * @prop {Element} notification_container The container where to display notifications
 * @prop {Object} contract_instance The contract instance, which is filled by this component after the contract ABI parsing and changed after the contract is deployed
 * @prop {Web3Wrappers} web3_wrappers The object to represent different web3 wrappers
 * @private @prop {Object} solc_worker_ The solc compiler works in that Web Worker
 * @private @prop {Object} compiled_contract_ The solc output, see pls the {@link https://docs.soliditylang.org/en/v0.5.0/using-the-compiler.html#output-description}
 * @private @prop {Object} contract_filename_ A contract name with an extension
 * @private @prop {Object} contract_filename_short_ A contract name without an extension
 * @private @prop {number} gas_price_gwei_
 * @private @prop {Array<(string|Object)>} errors_
 * @private @prop {Array<(string|Object)>} successes_
 */
@customElement('dapp-compile-deploy-contract')
export default class dappCompileDeployContract extends LitElement {
  @property({type: Boolean, reflect: true}) has_logined = false;
  @property({type: String, reflect: true}) theme = "light";
  @property({type: String, reflect: true}) current_address = null;
  @query("kor-icon", true) open_button;
  @query("input", true) file_input;
  @query("kor-modal", false) deployment_form;
  /// Don't reflect these properties to HTML attributes (avoid a big data's duplication),
  /// don't update this component on these properties changes
  @property({type: Object, reflect: false, noAccessor: true}) notification_container = null;
  @property({type: Object, reflect: false, noAccessor: true}) contract_instance = null;
  @property({type: Object, reflect: false, noAccessor: true}) web3_wrappers = null;
  /// Private
  @property({type: Object, reflect: false, noAccessor: true, state: true}) solc_worker_ = null;
  @property({type: Object, reflect: false, noAccessor: true, state: true}) compiled_contract_ = null;
  @property({type: String, reflect: false, noAccessor: true, state: true}) contract_filename_ = null;
  @property({type: String, reflect: false, noAccessor: true, state: true}) contract_filename_short_ = null;
  @property({type: Number, reflect: false, noAccessor: true, state: true}) gas_price_gwei_ = 1;
  @property({type: Array, reflect: false, noAccessor: true, state: true}) errors_ = [];
  @property({type: Array, reflect: false, noAccessor: true, state: true}) successes_ = [];
  ///

  static get styles() {
    return css`
      .dark input[type="file"] {
        filter: invert(100%);
      }`;
  }

  render() {
    return html`
      <kor-icon icon="get_app" button style="display: none"
        @click="${(e)=>{
          this.deployment_form.setAttribute("visible", "true");
        }}"
      ></kor-icon>
      <kor-modal icon="get_app" label="Let's compile & deploy a smart contract" theme="${this.theme}">
        <kor-card flat>
          <kor-grid columns="1" fit-content>
            <kor-card grid-cols="1" flat>
              <kor-input label="Gas price in GWEI for the deployment" type="number" min=1 value="${this.gas_price_gwei_}"
                @change="${(e)=>{
                  this.gas_price_gwei_ = parseInt(e.target.value);
                }}"
              ></kor-input>
              <input type="file"
                @change="${(e)=>{
                  /** @const {Element} */
                  const self = e.target;
                  /** @const {Object} */
                  const file_reader  = new FileReader();
                  file_reader.onloadend = () => {
                    if (!file_reader.result) {
                      this.errors_ = this.errors_.concat("A file reading error");
                    }
                    if (!window.ethereum) {
                      this.errors_ =
                        this.errors_.concat("A blockchain provider hasn't found in the window.ethereum, install Metamask please");
                    }
                    if (this.errors_.length) {
                      Utils.error_notifications(this.errors_, this.notification_container);
                      return;
                    }
                    /** A structured input for the Solidity Compiler, see pls the
                     * {@link https://github.com/ethereum/solc-js}
                     * @const {Object} */
                    const solc_input = {
                      language: 'Solidity',
                      sources: {
                        [this.contract_filename_]: {
                          content: file_reader.result
                        }
                      },
                      settings: {
                        outputSelection: {
                          '*': {
                            '*': ['*']
                          }
                        }
                      }
                    };
                    console.log("The solc input == ", solc_input);
                    this.solc_worker_.postMessage(JSON.stringify(solc_input));
                  }; // file_reader.onloadend
                  if (!self.files[0] || !self.files[0].name) {
                    this.errors_ = this.errors_.concat("Please specify a file with a contract's code to compile");
                    Utils.error_notifications(this.errors_, this.notification_container);
                    return;
                  }
                  this.contract_filename_ = self.files[0].name;
                  // Remove everything since the last dot
                  this.contract_filename_short_ = this.contract_filename_.replace(/\.[^.]+$/, '');
                  file_reader.readAsText(self.files[0]);
                }}"
              >
            </kor-card>
          </kor-grid>
        </kor-card>
        <kor-button label="Estimate a gas usage" color="Primary" slot="footer" disabled="true"
          @click="${(e)=>{
            this.contract_instance.deploy({
              data: this.compiled_contract_.contracts[this.contract_filename_][this.contract_filename_short_].evm.bytecode.object
            }).estimateGas((error, gas) => {
              if (error) {
                this.errors_ = this.errors_.concat(error);
                Utils.error_notifications(this.errors_, this.notification_container);
              };
              if (gas) {
                this.successes_ = this.successes_.concat("The contract deployment will use [" + gas + "] gas");
                Utils.success_notifications(this.successes_, this.notification_container);
              }
            });
          }}"
        ></kor-button>
        <kor-button label="Deploy the contract" color="Primary" slot="footer" disabled="true"
          @click="${(e)=>{
            if (!this.current_address || this.current_address.toString().length != 42) {
              this.errors_ = this.errors_.concat("Please, fill a correct address from which the contract will be deployed");
              Utils.error_notifications(this.errors_, this.notification_container);
              return;
            }
            Utils.send_transaction(
              this.contract_instance.deploy({
                data: this.compiled_contract_.contracts[this.contract_filename_][this.contract_filename_short_].evm.bytecode.object
              }), // A transaction object to pass as an argument
              this.current_address, this.gas_price_gwei_, 0 /* value_gwei */,
              this.errors_, this.successes_, this.notification_container
            ).then((new_contract_instance)=>{
              this.successes_ = this.successes_.concat("The contract was deployed at address == "
                + new_contract_instance.options.address.toString());
              Utils.success_notifications(this.successes_, this.notification_container);
              this.contract_instance = new_contract_instance;
              this.dispatchEvent(new Event('contract-deployed',
                {bubbles: true, composed: true}));
            });
            this.deployment_form.removeAttribute("visible");
          }}"
        ></kor-button>
      </kor-modal>`;
  }

  /** Handles the message from the solc Web Worker with the compiled_contract
   * @func handleCompiledContract
   * @param {string} compiled_contract */
  handleCompiledContract(message) {
    this.compiled_contract_ = JSON.parse(message.data);
    console.log("The solc output == ", this.compiled_contract_);
    if (!this.compiled_contract_.contracts
        || !this.compiled_contract_.contracts[this.contract_filename_]
        || !this.compiled_contract_.contracts[this.contract_filename_]
        || !this.compiled_contract_.contracts[this.contract_filename_][this.contract_filename_short_])
    {
      this.errors_ =
        this.errors_.concat(`The contract ${this.contract_filename_} can't be compiled`);
      if (this.compiled_contract_.errors) {
        this.errors_ =
          this.errors_.concat(this.compiled_contract_.errors[0].formattedMessage);
      }
      Utils.error_notifications(this.errors_, this.notification_container);
      return;
    }
    this.contract_instance = new this.web3_wrappers.metamask.eth.Contract(
      this.compiled_contract_.contracts[this.contract_filename_][this.contract_filename_short_].abi);
    if (!this.contract_instance) {
      this.errors_ = this.errors_.concat("A contract instance creation error");
      Utils.error_notifications(this.errors_, this.notification_container);
      return;
    }
    /** @const {NodeList} */
    const buttons =
      this.shadowRoot.querySelectorAll('kor-button');
    Array.from(buttons).map((button)=>{
      button.removeAttribute("disabled");
    });
  }

  constructor() {
    super();
    if (!window.Worker) {
      this.errors_ =
        this.errors_.concat("This browser doesn't support the dedicated Web Worker, which is needed for the Solidity Compiler, therefore this component won't work.");
      Utils.error_notifications(this.errors_, this.notification_container);
      return;
    }
    /** Create an inlined Web Worker
     * @const {Object} */
    const blob = new Blob([`
      /** Injects the Solidity Compiler of the latest version (0.8.4)
       *  into the self.Module object avoiding a CORS blocking
       *  and UI thread blocking of the WebAssembly.compile invoking */
      importScripts('https://solc-bin.ethereum.org/bin/soljson-latest.js');
      console.log("The Solidity Compiler (solc-js) object == ", Module);
      if (!Module.cwrap || !Module._solidity_compile || !Module._solidity_reset) {
        throw "The self.Module hasn't crucial properties, which are needed for the Solidity Compiler";
      }
      if (Module._solidity_version) {
        /** @const {Function} */
        const solidity_version = Module.cwrap('solidity_version', 'string', []);
        console.log("The solidity version == ", solidity_version());
      }
      /** The objects below were fetched from this code:
       * {@link https://github.com/ethereum/solc-js/blob/master/wrapper.js}
       * @const {Object} */
      const compile =
        Module.cwrap('solidity_compile', 'string', ['string', 'number']);
      /** @const {Object} */
      const reset = Module.cwrap('solidity_reset', null, []);
      onmessage = (e) => {
        /** @const {Object} */
        const output = compile.apply(undefined, [e.data]);
        reset();
        postMessage(output);
      };
    `]);
    /** Obtain a blob URL reference to our worker 'file'.
     * @const {Object} */
    const blobURL = window.URL.createObjectURL(blob);
    this.solc_worker_ = new Worker(blobURL);
    this.solc_worker_.addEventListener('error', (e)=>{
      this.errors_ =
        this.errors_.concat(`Solc worker error: line ${e.lineno} in ${e.filename} : ${e.message}`);
      Utils.error_notifications(this.errors_, this.notification_container);
    });
    this.solc_worker_.addEventListener(
      'message', (message)=>{this.handleCompiledContract(message);});
  }

  connectedCallback() {
    super.connectedCallback();
    Utils.setup_component(this);
  }

  attributeChangedCallback(name, oldval, newval) {
    super.attributeChangedCallback(name, oldval, newval);
    if (name == 'has_logined') {
      if (this.has_logined == true) {
        this.errors_ = [];
        this.successes_ = [];
        Utils.login_component(this);
        this.open_button.style.display = "block";
      } else {
        this.open_button.style.display = "none";
        Utils.logout_component(this);
      }
    }
    if (name == 'theme' && this.deployment_form) {
      this.deployment_form.theme = this.theme;
      if (this.theme == "dark") {
        file_input.classList.add("dark");
      } else {
        file_input.classList.remove("dark");
      }
    }
    if (name == 'current_address') {
      this.successes_ =
        this.successes_.concat("A contract will be deployed from that address == " + this.current_address);
      Utils.success_notifications(this.successes_, this.notification_container);
    }
    this.dispatchEvent(new Event(`${name}-changed`));
  }
}
