import {LitElement, html, css, render as LitRender} from 'lit';
import {customElement, property, query} from 'lit/decorators.js';
import '@kor-ui/kor';
import TransactionsTemplates from '../templates/transactions.js';
import Utils from '../utils.js';

/** @fileoverview The component for a transaction adding,
 * it consists of an icon-button, a tooltip and a modal window with a form to fill
 * Generates the ${name}-changed event when a property ${name} was changed
 * @externs
 * @module
 * @prop {boolean} has_logined Whether a user has logined
 * @prop {string} theme The "light" and "dark" enumeration
 * @prop {string} current_address The address, from which the transaction will be sent
 * @prop {Element} add_transaction The icon-button of this component, which shows the transaction form
 * @prop {Element} add_transaction_form The form with a transaction data
 * @prop {Element} notification_container This component notification container
 * @prop {Element} transaction_content The dynamically generated content container
 * @prop {BookedAddresses} booked_addresses The addresses already used to make transactions as an instance of the BookedAddresses class backed by the Local Storage
 * @prop {Web3Wrappers} web3_wrappers The object to represent different web3 wrappers
 * @private @prop {TransactionData} transaction_data_ The transaction's details temporary object
 * @see module:../type_def.h
 */
@customElement('dapp-add-transaction')
export default class dappAddTransaction extends LitElement {
  @property({type: Boolean, reflect: true}) has_logined = false;
  @property({type: String, reflect: true}) theme = "light";
  @property({type: String, reflect: true}) current_address = null;
  @query("#addTransaction", true) add_transaction;
  @query("kor-tooltip", true) tooltip;
  @query("#addTransactionForm", false) add_transaction_form;
  @query("#notificationContainer", true) notification_container;
  @query("#transactionContent", true) transaction_content;
  /// Don't reflect these properties to HTML attributes (avoid a big data's duplication),
  @property({type: Object, reflect: false}) booked_addresses = null;
  /// don't update this component on these properties changes
  @property({type: Object, reflect: false, noAccessor: true}) web3_wrappers = null;
  /// Private
  @property({type: Object, reflect: false, noAccessor: true, state: true}) transaction_data_ = {
    gas_price_gwei: 1,
    gas_limit: 21000
  };
  ///

  render() {
    return html`
      <kor-icon id="addTransaction" icon="credit_card" button style="display: none"
        @click="${(e)=>{
          this.tooltip.setAttribute("visible", "false");
          this.web3_wrappers.metamask.eth.getBalance(this.current_address).then((account_balance)=>{
            LitRender(TransactionsTemplates.add_transaction(
                       account_balance, [] /* TODO: own_addresses */,
                       this.booked_addresses, this.transaction_data_, true /* has_destination */),
                       this.transaction_content);
            this.add_transaction_form.setAttribute("visible", "true");
          });
        }}"
      ></kor-icon>
      <kor-tooltip position="left">Add a transaction</kor-tooltip>
      <kor-modal id="addTransactionForm" icon="credit_card" label="Add a transaction" theme="light"
        @visible-changed="${(e)=>{
          if (!e.target.visible) {
            /** Hide the notification container of this component together with it's modal window
             * @const {?Element} */
            const notification_item =
              this.notification_container.querySelector('kor-notification-item');
            if (notification_item) notification_item.removeAttribute("visible");
          }
        }}"
      >
        <div id="notificationContainer"></div>
        <kor-card flat>
          <kor-grid columns="1" fit-content>
            <kor-card id="transactionContent" grid-cols="1" flat></kor-card>
          </kor-grid>
        </kor-card>
        <kor-button label="Send" color="Primary" slot="footer"
          @click="${(e)=>{
            this.handleAddTransaction();
          }}"
        ></kor-button>
      </kor-modal>`;
  }

  handleAddTransaction() {
    if (!Utils.check_transaction_data(
      this.transaction_data_, this.notification_container, true /* has_destination */)) return;
    this.add_transaction_form.removeAttribute("visible");
    console.log("The transaction's destination_address == ", this.transaction_data_.destination_address);
    console.log("The transaction's amount in GWEI == ", this.transaction_data_.amount_gwei);
    /** The transaction object literal
     * @struct @const {Object} */
    const transactionObject = {
      /** @const {string|number} */
      from: this.current_address,
      /** @const {string} */
      to: this.transaction_data_.destination_address,
      /** @const {number} */
      value: (this.transaction_data_.amount_gwei * Math.pow(10, 9)),
      /** @const {number} */
      gasPrice: (this.transaction_data_.gas_price_gwei * Math.pow(10, 9)),
      /** @const {number} */
      gas: this.transaction_data_.gas_limit
    };
    console.log("The transactionObject == ", transactionObject);
    this.web3_wrappers.metamask.eth.sendTransaction(transactionObject, (error, result) => {
      console.log("The sendTransaction error == ", error);
      console.log("The sendTransaction result == ", result);
      if (!error) {
        this.booked_addresses.add(this.transaction_data_.destination_address);
        Utils.success_notifications(
          ["The transaction was sent and will be shown in the Transactions tab after processing"],
          this.shadowRoot.page_notification);
        /** @const {Element} */
        const notification_item =
          this.shadowRoot.page_notification.querySelector('kor-notification-item');
        notification_item.setAttribute("visible", "true");
      }
      // Create a new object by copying of inner values
      /** @type {Object} */
      let tmp_data = {};
      Object.assign(tmp_data, this.transaction_data_)
      this.transaction_data_ = tmp_data;
    });
  }

  constructor() {
    super();
  }

  connectedCallback() {
    super.connectedCallback();
    /** @const {NodeList} */
    const undefinedElements =
      this.shadowRoot.querySelectorAll(':not(:defined)');
    /** @const {Array<Promise>} */
    const promises = [...undefinedElements].map(element => {
      return customElements.whenDefined(element.localName);
    });
    /// Invoke the code below after this.shadowRoot is rendered
    Promise.all(promises).then(() => {
      Utils.setup_component(this);
      this.tooltip.target = this.add_transaction;
    });
  }

  attributeChangedCallback(name, oldval, newval) {
    super.attributeChangedCallback(name, oldval, newval);
    if (name == 'has_logined') {
      if (this.has_logined == true) {
        Utils.login_component(this);
        this.add_transaction.style.display = "block";
      } else {
        this.add_transaction.style.display = "none";
        Utils.logout_component(this);
      }
    }
    if (name == 'theme' && this.add_transaction_form) {
      this.add_transaction_form.theme = this.theme;
    }
    this.dispatchEvent(new Event(`${name}-changed`));
  }
}
