import {LitElement, html} from 'lit';
import {customElement, property, query} from 'lit/decorators.js';
import '@kor-ui/kor';

/** @fileoverview The component for a tab strip rendering
 * @externs
 * @module
 * @prop {Array<Tab>} tabs The array with tabs objects
 * @prop {Element} first_tab
 * @see module:../type_def.h
 */
@customElement('dapp-tab-strip')
export default class dappTabStrip extends LitElement {
  @property({type: Array, reflect: false}) tabs = [];
  @query('kor-tab-item', true) first_tab;

  render() {
    return html`
      <kor-tabs orientation="horizontal">
        ${this.tabs.map((tab, index) => html`
          <kor-tab-item label="${tab.label}"
            @active-changed="${(e)=>{
              if (e.target.active) {
                tab.component.visible = true;
              } else {
                tab.component.visible = false;
              }
            }}"
          ></kor-tab-item>`)}
      </kor-tabs>`;
  }

  constructor() {
    super();
  }

  connectedCallback() {
    super.connectedCallback()
    /** @const {NodeList} */
    const undefinedElements =
      this.shadowRoot.querySelectorAll(':not(:defined)');
    /** @const {Array<Promise>} */
    const promises = [...undefinedElements].map(element => {
      return customElements.whenDefined(element.localName);
    });
    /// Invoke the code below after this.shadowRoot is rendered
    Promise.all(promises).then(() => {
      this.first_tab.setAttribute("active", true);
    });
  }
}
